<template>
  <label class="uc-checkbox" :class="{readonly}">
    <input type="checkbox" ref="input" :name="id" :id="id" :disabled="readonly" :checked="checkedValue" :value="guid" @change="onChange" v-show="!compact">
    <div class="uc-checkbox__check" :class="{compact}">
      <check class="check-mark" v-if="!dual"></check>
      <check-dual v-if="dual"></check-dual>
    </div>
    <span v-truncate class="label" v-if="!compact">{{label}}</span>
  </label>
</template>

<script>
import GetId from '../../js/id-generator.js'
import Check from '../../icons/check.vue'
import CheckDual from '../../icons/check-dual.vue'
export default {
  name: 'uc-checkbox',
  data () {
    return {
      id: null
    }
  },
  props: {
    label: String,
    readonly: Boolean,
    checked: Boolean,
    guid: String,
    tooltip: String,
    indeterminate: {
      type: Boolean,
      default: false
    },
    dual: {
      type: Boolean,
      default: false
    },
    compact: {
      type: Boolean,
      default: false
    }
  },
  components: {
    Check,
    CheckDual
  },
  watch: {
    indeterminate () {
      this.$refs.input.indeterminate = this.indeterminate
    }
  },
  computed: {
    checkedValue () {
      return this.checked ? 'checked' : ''
    }
  },
  methods: {
    onChange (event) {
      this.$emit('change', { value: event.target.checked, label: this.label, guid: this.guid })
    }
  },
  created () {
    this.id = GetId()
  }
}
</script>

<style lang="stylus" scoped>
  .uc-checkbox
    cursor pointer
    position: relative
    display flex
    align-items center
    font-size $input_font_size
    font-weight 600
    &.readonly
      cursor not-allowed
    input[type=checkbox]
      position: absolute
      visibility: hidden
    input[type=checkbox]:not(:checked)
      & ~ .uc-checkbox__check svg
        /deep/ svg
          display none
  .uc-checkbox__check
    display flex
    .compact &
      margin-top: -9px
      margin-left: 6px
    svg
      object-fit contain
      margin: auto
    .readonly &
      opacity .6
  .label
    max-width 700px
    margin-left 10px
    primary-body-text()
</style>
