<template>
  <div ref="node" tabindex="0"
    :id="cat.guid"
    class="cat-builder__node"
    :class='classes'
    @click='clickHandler'>
    <span v-truncate>{{cat.name}}</span>
    <right-arrow v-show='cat.hasChildren'></right-arrow>
  </div>
</template>

<script>
import RightArrow from '../../../shared/icons/triangle-right.vue'
export default {
  name: 'tree-node',
  data () {
    return {
      hoverMsBeforeLoad: 1000,
      dragOverTimestamp: null,
      emitTypes: {
        selected: 'selected',
        changeSection: 'changeSection'
      },
      isDropzone: false,
      dragClone: null
    }
  },
  props: {
    cat: Object,
    catIndex: Number,
    parentGuid: String
  },
  computed: {
    classes () {
      return {
        selected: this.cat.selected,
        displaying: this.cat.displaying
      }
    },
    height () {
      return this.$el.clientHeight
    }
  },
  components: {
    RightArrow
  },
  methods: {
    clickHandler (e) {
      e.stopPropagation()
      this.setFocus()
      this.select()
    },

    setFocus () {
      this.$el.focus()
    },

    select () {
      this.$emit(this.emitTypes.selected, {
        parentGuid: this.parentGuid,
        cat: this.cat
      })
    }
  }
}
</script>

<style lang='stylus' scoped>
.cat-builder__node
  display: flex
  justify-content: space-between
  width: 100%
  padding: 0 2px 0 10px
  align-items: center
  font-size: 0.8125rem
  font-weight: 600
  cursor: pointer
  height: $treenode_height
  outline none
  border 1px dashed rgba(0, 0, 0, 0)
  transition transform 50ms ease, border-color 200ms cubic-bezier(0.600, 1.290, 0.605, -0.525)
  span
    max-width 90%
    display inline-block
    padding 1px 0
  svg
    fill: $base_text_col
    box(10px)
    pointer-events none
  &.selected, &:hover
    background-color: $col_light_selection
  &.displaying
    background-color $col_dark_selection
    filter: opacity(60%)
    color: #fff
    svg
      fill: #fff
    &:focus
      background-color $col_dark_selection
      filter none
  &.dragging
    background-color: #eee !important
    background-image: linear-gradient(45deg, #f6f6f6 25%, transparent 25%, transparent 75%, #f6f6f6 75%, #f6f6f6), linear-gradient(45deg, #f6f6f6 25%, transparent 25%, transparent 75%, #f6f6f6 75%, #f6f6f6) !important
    background-size: 16px 16px
    background-position: 0 0, 8px 8px
    color #fff !important
    cursor move
    svg
      fill: #fff !important
  &.dropzone
    border 1px dashed rgba(0, 0, 0, 0.5)
  &.recent-drop
    background-color yellow
.drag-clone
  background-image none !important
  background none !important
  background-color $col_dark_selection !important
  color #fff
  position absolute
  width 300px
  left -9999px
  svg
      fill: #fff

</style>
