<template>
  <div class="details__info">
    <div class="details__info-wrapper" :class="{toggled: toggled}" @click="toggleInfo">
      <cog class="cog"></cog>
    </div>
    <transition name="popover-right">
      <div v-show="toggled" class="dropdown-overlay details__info-dropdown">
        <slot></slot>
      </div>
    </transition>
  </div>
</template>

<script>
import cog from '../../../shared/icons/coq'
export default {
  name: 'info',
  data () {
    return {
      toggled: false
    }
  },
  components: {
    cog
  },
  methods: {
    hide () {
      this.toggled = false
    },
    toggleInfo (event) {
      event.preventDefault()
      this.toggled = !this.toggled
      if (this.toggled) {
        this.$nextTick(() => {
          window.addEventListener('click', this.closeHandler)
          this.$el.addEventListener('click', function (e) { e.stopPropagation() })
        })
      }
    },
    closeHandler () {
      this.toggled = false
      window.removeEventListener('click', this.closeHandler)
    }
  }
}
</script>

<style lang="stylus">
.details__info
  display inline-block
  position relative
  font-size 0.8125rem
  white-space nowrap

.details__info-wrapper
  border-radius 4px
  padding 4px 8px
  transition background-color 200ms
  &.toggled
    background-color $col_light_selection

.details__info-icon
  circle(13px)
  background-color $col_light_gray
  color #fff
  font-weight bold
  text-align center
  line-height 13px
  display inline-block
  cursor pointer
  margin-right 7px
  font-size 10px

.details__header-label
  font-size 0.8125rem
  cursor pointer
  color $col_primary

.details__info-dropdown
  width 345px
  padding 8px 15px 15px
  right 0
  left unset !important
  line-height 1.5
  font-size 0.75rem
  z-index 100
  label
    margin-top 15px
    &:first-child
      margin-top 5px
.cog
  width 12px
  height 12px
  margin 0 4px
  padding-top 4px
  fill $col_medium_gray
</style>
