import { localStore } from './storage'
import Constants from './constants'

export default {
  // localStorage helpers
  getLocalStorageData (currentList) {
    const enums = Constants.GenericList
    const key = currentList.localStorageKey
    return {
      defaultOrder: localStore.getJSON(`${key}-${enums.DefaultOrder}`) || [],
      currentOrder: localStore.getJSON(`${key}-${enums.CurrentOrder}`) || [],
      configuration: localStore.getJSON(`${key}-${enums.Configuration}`) || {}
    }
  },

  setLocalStorageKeyData (currentList, keySuffix, data) {
    const key = currentList.localStorageKey
    localStore.set(`${key}-${keySuffix}`, JSON.stringify(data))
  },

  // mutations helpers
  saveColumnOrder (currentList) {
    let columnsToShow = currentList.columns
      .filter(x => x.show)
      .map(x => x.key)
    if (columnsToShow.length !== 0) {
      let localStorageKey = currentList.localStorageKey
      if (localStorageKey) {
        this.setLocalStorageKeyData(
          currentList,
          Constants.GenericList.CurrentOrder,
          columnsToShow)
      }
    }
  },

  setDefaultColumnOrder (currentList, defaultColumnNames, defaultColumnsCount) {
    let defaultColumns = []
    if (defaultColumnNames.length !== 0) {
      defaultColumns = defaultColumnNames
    } else {
      defaultColumns = currentList.columns
        .slice(0, defaultColumnsCount)
        .map(x => x.key)
    }
    this.setLocalStorageKeyData(
      currentList,
      Constants.GenericList.DefaultOrder,
      defaultColumns)
  },

  // actions helpers
  mergeColumns (currentList, columnNames) {
    let columnsToHide = currentList.columns.filter(x => !columnNames.includes(x.key))
    let columnsToShow = currentList.columns.filter(x => columnNames.includes(x.key))
    columnsToShow = this.orderColumns(columnsToShow, columnNames)
    return [...columnsToShow, ...columnsToHide]
  },

  orderColumns (columns, columnNames) {
    let orderedColumns = []
    columnNames.forEach(columnName => {
      let column = columns.find(x => x.key === columnName)
      if (column) orderedColumns.push(column)
    })
    return orderedColumns
  }
}
