const getters = {
  treeSections: state => state.treeSections,
  sectionWidth: state => state.sectionWidth,
  sectionHeight: state => state.sectionHeight,
  dragInfo: state => state.dragInfo,
  displayedNode: state => state.displayedNode,
  displayedNodePosition: state => {
    const pos = { section: 0, index: 0 }
    let index = state.treeSections.length
    while (index--) {
      const foundIndex = state.treeSections[index].categories.findIndex(
        x => x.displaying
      )
      if (foundIndex > -1) {
        pos.section = index
        pos.index = foundIndex
        return pos
      }
    }
    return pos
  },
  selectedCatalog: state => {
    if (!state.treeSections[0]) return {}
    return state.treeSections[0].categories.find(x => x.selected)
  },
  selectedStore: state => state.selectedStore,
  stores: state => state.stores
}

export default getters
