<template>
  <label class="uc-radio" :class="{readonly}">
    <input type="radio" :name="group" :id="id" :disabled="readonly" :value="guid" :checked="checked" @change="onChange">
    <radio class="radio-icon"></radio>
    <span v-truncate class="label">{{label}}</span>
  </label>
</template>

<script>
import GetId from '../../js/id-generator.js'
import Radio from '../../icons/radio.vue'

export default {
  name: 'uc-radio',
  data () {
    return {
      id: null
    }
  },
  props: {
    label: String,
    readonly: Boolean,
    checked: Boolean,
    group: String,
    guid: String
  },
  components: {
    Radio
  },
  methods: {
    onChange (event) {
      this.$emit('change', { label: this.label, guid: this.guid })
    }
  },
  created () {
    this.id = GetId()
  }
}
</script>

<style lang="stylus">
.uc-radio
  cursor pointer
  position: relative
  display flex
  align-items center
  font-size $input_font_size
  font-weight 600
  &.readonly
    cursor not-allowed
  input[type=radio]
    position: absolute
    visibility: hidden
  input[type=radio]:not(:checked)
    & ~ .radio-icon svg
      display none

.uc-radio__check
  position: absolute
  left 0
  top 0
  .readonly &
    opacity .6
.label
  max-width 700px
  margin-left 10px
  primary-body-text()
</style>
