import deepCopy from '../../shared/js/deep-copy'

export function makeDefinitionsByDefinitionGuid (definitions) {
  return deepCopy(definitions).reduce((acc, definition) => {
    return { ...acc, [definition.definition.guid]: definition }
  }, {})
}

export function mergeAllData (definitionsByDefinitionGuid, entityList) {
  return entityList.map(entity => {
    const definition = definitionsByDefinitionGuid[entity.meta.definition.guid]

    const mergedEntity = Object.getOwnPropertyNames(definition).reduce((acc, definitionFieldName) => {
      const definitionField = definition[definitionFieldName]
      const property = entity[definitionFieldName]
      const mergedDefinitionFieldAndProperty = { [definitionFieldName]: { ...definitionField, ...property } }

      return { ...acc, ...mergedDefinitionFieldAndProperty }
    }, {})

    return { ...mergedEntity, ...{ meta: entity.meta } }
  })
}
