<template>
 <div class="content-selector-container">
    <div v-for="item in items" :key="item.guid" class="item" @click="selectItem(item)">
      <picker-image :item="item" :showName="!item.url" :sizeObj="{ value: 180, suffix: 'px'}" :selected="isSelected(item)"/>
    </div>
  </div>
</template>

<script>
import PickerImage from './picker-image'
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'content-selector',
  components: {
    PickerImage
  },
  props: {
    multiSelect: Boolean,
    preSelectedItems: {
      type: Array,
      default () {
        return []
      }
    }
  },
  mounted () {
    this.setSelectedItems(this.preSelectedItems)
  },
  computed: {
    ...mapGetters('navigator', ['treeSections', 'displayedNodePosition', 'selectedItems']),
    section () {
      return this.displayedNodePosition.section + 1
    },
    items () {
      if (!this.treeSections[this.section]) {
        return []
      } else {
        return this.treeSections[this.section].allCategories
      }
    },
    selectedItemsSummary () {
      const count = this.selectedItems.length
      return this.$tc('contentNavigator.itemsSelected', count)
    }
  },
  methods: {
    ...mapMutations('navigator', ['toggleSelectedItem', 'setSelectedItems']),
    isSelected (item) {
      return this.selectedItems.includes(item.guid)
    },
    selectedItemPosition (item) {
      return this.selectedItems.indexOf(item.guid) + 1
    },
    selectItem (item) {
      const firstSelected = this.selectedItems[0]
      if (!this.multiSelect && firstSelected) {
        this.toggleSelectedItem(firstSelected)
      }
      this.toggleSelectedItem(item.guid)
    }
  }
}
</script>

<style lang="stylus" scoped>
.content-selector-container
  display flex
  flex-wrap wrap
  overflow-y auto

.item
  margin-right 12px
  margin-bottom 12px
  box(180px)
</style>
