import Helpers from '../../../shared/js/generic-list-helpers'
import Constants from '../../../shared/js/constants'

const actions = {
  applyColumnOrder ({ commit }, { id, currentList }) {
    let columns = []
    let columnNames = []
    const LSD = Helpers.getLocalStorageData(currentList)
    if (LSD.currentOrder.length !== 0) {
      columns = Helpers.mergeColumns(currentList, LSD.currentOrder)
      columnNames = LSD.currentOrder
    } else {
      columns = Helpers.mergeColumns(currentList, LSD.defaultOrder)
      columnNames = LSD.defaultOrder
    }
    commit('setListColumns', { id: id, columns: columns })
    commit('toggleListColumns', { id: id, columnNames: columnNames })
  },

  applyDefaultColumnOrder ({ dispatch }, { id, currentList }) {
    Helpers.setLocalStorageKeyData(currentList, Constants.GenericList.CurrentOrder, [])
    dispatch('applyColumnOrder', { id: id, currentList: currentList })
  },

  saveColumnClientConfiguration ({ dispatch }, { currentList, columnKey, fieldName, fieldValue }) {
    let dict = Helpers.getLocalStorageData(currentList).configuration
    if (!dict[columnKey]) {
      dict[columnKey] = {}
    }
    dict[columnKey][fieldName] = fieldValue
    Helpers.setLocalStorageKeyData(currentList, Constants.GenericList.Configuration, dict)
  },
  getColumnsClientConfiguration ({ dispatch }, { currentList }) {
    return Helpers.getLocalStorageData(currentList).configuration
  }
}
export default actions
