<template>
  <div class="settings-container">
    <btn-round size="34px" @click="toggleSettingsMenu">
      <icon-more></icon-more>
    </btn-round>
    <nav v-if="toggled" class="settings-menu" :class="{'show': toggled}">
      <div class="arrow-up"></div>
      <div class="settings-menu_search">
        <uc-text-input class="settings-menu_search_input" :placeholder="'Search'" label="Search Properties" :value="searchTerm" @input="onSearch"/>
      </div>
      <div class="settings-menu_properties-lists">
        <simplebar class="settings__simplebar">
          <div v-if="searchTerm == ''">
            <properties-list :title="'Visible'" :properties="shownProperties" :onClickAll="false" :listId="listId"/>
            <properties-list :title="'Not visible'" :properties="hiddenProperties" :onClickAll="true" :listId="listId"/>
          </div>
          <div v-if="searchTerm != '' && foundProperties.length > 0">
            <properties-list :title="foundTitle" :properties="foundProperties" :onClickSingle="true" :listId="listId"/>
          </div>
          <div v-if="searchTerm != '' && foundProperties.length == 0" class="no-results">No match found</div>
        </simplebar>
      </div>
    </nav>
  </div>
</template>

<script>
import BtnRound from '../../../shared/components/widgets/btn-round'
import IconMore from '../../../shared/icons/more'
import PropertiesList from './properties-list'
import UcTextInput from '../../../shared/components/forms/uc-text-input'
import simplebar from 'simplebar-vue'
import { createNamespacedHelpers } from 'vuex'
const genericListStore = createNamespacedHelpers('genericList')

export default {
  name: 'Settings',
  components: {
    BtnRound,
    IconMore,
    PropertiesList,
    UcTextInput,
    simplebar
  },
  props: {
    listId: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      toggled: false,
      searchTerm: '',
      listData: null,
      settingsItem: null
    }
  },
  mounted () {
    this.settingsItem = this.$el
  },
  created () {
    this.listData = this.list(this.listId)
  },
  computed: {
    ...genericListStore.mapGetters(['list']),
    foundProperties () {
      return this.listData.columns.filter(x => x.label.toLowerCase().includes(this.searchTerm))
    },
    foundTitle () {
      return `Found ${this.foundProperties.length} matches`
    },
    shownProperties () {
      return this.listData.columns.filter(x => x.show)
    },
    hiddenProperties () {
      return this.listData.columns.filter(x => !x.show)
    }
  },
  methods: {
    ...genericListStore.mapActions(['applyDefaultColumnOrder']),
    toggleSettingsMenu () {
      // Very limited implementation. this could be elevated to a directive
      // Adding event listener to collect click outside.
      // Removing event listener when setting are closed.

      if (!this.toggled) {
        window.addEventListener('click', this.closeSettingsFromOutside)
      }

      this.searchTerm = ''
      this.toggled = !this.toggled
    },
    selectDefault () {
      this.searchTerm = ''
      this.applyDefaultColumnOrder({ id: this.listId, currentList: this.listData })
    },
    onSearch (value) {
      this.searchTerm = value.toLowerCase()
    },

    closeSettingsFromOutside (e) {
      // console.log('Window Listener Added', e.path, this.settingsItem)
      // this.toggleSettingsMenu()
      let element = this.settingsItem
      if (e.target !== element && !element.contains(e.target)) {
        this.toggleSettingsMenu()
        window.removeEventListener('click', this.closeSettingsFromOutside)
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
  .uc-text-input
    .input-field-container
      border-radius 22px
      .input-field-wrapper
        border-radius 22px

  $property-list_min-height = 250px
  $property-list_max-height = 245px

  .outside-mask
    width 100%
    height 100%
    background-color red
    position relative
    left 0
    right 0
    top 0
    bottom 0

  .arrow-up
    position relative
    width 0
    height 0
    border-left 10px solid transparent
    border-right 10px solid transparent
    border-bottom 10px solid #fff
    top -10px
    margin 0 calc(100% - 26px)

  .settings__simplebar
    min-height $property-list_min-height
    max-height $property-list_max-height

  .settings-container
    display flex
    flex-direction column
    position relative
    z-index 102

  .settings-trigger
    display flex
    align-items center
    margin-left auto
    cursor pointer

  .settings-icon
    circle(26px)
    display flex
    transition background-color 200ms
    svg
      box(80%)
      fill rgba(0,0,0,0.2)
      margin auto
    &:hover
      background-color $col_hover1

  .settings-label
    color $col_label
    &:hover
      text-decoration: underline

  .settings-menu
    position absolute
    right 0
    width 240px
    top 50px
    background-color $col_bg
    pointer-events none
    box-shadow 0 4px 30px 0 rgba(0, 0, 0, 0.15)
    z-index 20
    transform translate(-11px, -11px) scale(0)
    &.show
      transform translate(0, 0) scale(1)
      pointer-events all

  .settings-menu_search
    padding 10px 20px 20px 20px
    width auto
    border-bottom solid 1px $input-field-stroke

  .search-icon-container
    box(18px)
    padding 9px
    position relative
    opacity 0.5

  .no-results
    font-size 16px
    text-align center
    margin-top 20px
    height $property-list_min-height
    line-height 20px
    color $col_grey0
    width 100%
    height 100%

  .actions
    display flex
    justify-content space-between
    padding 12px
    border-top 1px solid $input-field-stroke
</style>
