<template>
  <div class="uc-image" :class="[{cover}, {readonly}]" @click="clickHandler">
    <img v-if="!fallback && src" :class="[{cover}, {hidden: hideImage}]" :src="src" :alt="alt" @error="showDefault">
    <camera-icon v-if="fallback" />
    <folder-icon v-if="!src && !fallback" class="folder"/>
  </div>
</template>

<script>
import CameraIcon from '../icons/camera'
import FolderIcon from '../icons/folder-image'

export default {
  name: 'uc-image',
  data () {
    return {
      fallback: false
    }
  },
  props: {
    src: String,
    alt: String,
    cover: Boolean,
    readonly: Boolean
  },
  computed: {
    hideImage () {
      return this.src.toString() === '/umbraco/ucommerce/images/ui/image_not_found.jpg'
    }
  },
  components: {
    CameraIcon,
    FolderIcon
  },
  methods: {
    showDefault () {
      this.fallback = true
    },
    clickHandler () {
      this.$emit('click')
    }
  }
}
</script>

<style lang="stylus" scoped>
.uc-image
  center()
  cursor pointer
  padding 6px
  background-color $col_light_bg
  &.readonly
    cursor default
    background-color transparent
  img
    max-width 100%
    max-height 100%
    width auto
    height auto
  svg
    box(45%)
    display block
    margin auto
    opacity .4
  .hidden
    display none
</style>
