<template>
  <div class="picker-image" :style="{ width: size, height: size}" @mouseover="hovered = true" @mouseleave="hovered = false">
    <uc-image class="stretch" :src="item.url" :alt="item.name"/>
    <div v-show="selected" class="background stretch"/>
    <div v-show="selected" class="badge label-container">{{ selectedItemPosition }}</div>
    <div v-show="nameShown" class="name label-container">{{ item.name }}</div>
  </div>
</template>

<script>
import UcImage from '../../../shared/components/uc-image'
import { mapGetters } from 'vuex'

export default {
  name: 'picker-image',
  data () {
    return {
      fallback: false,
      hovered: false
    }
  },
  props: {
    item: Object,
    sizeObj: {
      value: Number,
      suffix: String
    },
    selected: Boolean,
    showName: {
      type: Boolean,
      default: false
    }
  },
  components: {
    UcImage
  },
  computed: {
    ...mapGetters('navigator', ['selectedItems']),
    size () {
      return `${this.sizeObj.value}${this.sizeObj.suffix}`
    },
    selectedItemPosition () {
      return this.selectedItems.indexOf(this.item.guid) + 1
    },
    nameShown () {
      return this.showName || this.selected || this.hovered
    }
  },
  methods: {
    getSize (value) {
      return `${value}${this.sizeObj.suffix}`
    }
  }
}
</script>

<style lang="stylus" scoped>
.picker-image
  position relative
  &:hover
    outline 2px solid $col_dark_selection

.background
  background-color $col_dark_selection
  opacity .15
.label-container
  background-color #ffffff
  box-shadow 3px 0 6px $col_light_gray
  opacity .95
.badge
  right 0
  margin 6px
  box(32px)
  center()
.name
  bottom 0
  fit-label()

.stretch
  stretch()

.picker-image > .uc-image,
.picker-image > div
  position absolute
</style>
