<template>
  <transition name="flip-list">
    <div class="form-feedback" :class="classes" key="error"><slot class="slot"></slot></div>
  </transition>
</template>

<script>
import * as feedbackTypes from '../../js/feedback-types'
export default {
  name: 'form-feedback',
  props: {
    type: {
      type: String,
      default: feedbackTypes.info
    }
  },
  computed: {
    classes () {
      return {
        info: this.type === feedbackTypes.info,
        warning: this.type === feedbackTypes.warning,
        error: this.type === feedbackTypes.error
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.form-feedback
  z-index 9999
  font-size 1.275rem
  min-height 1.1rem
  padding 16px
  color #fff
  box-shadow 2px 2px 7px rgba(0,0,0,0.3)
  border-radius 3px
  position fixed
  width calc(100% - 32px)
  left 16px
  bottom 16px
  .slot
    width fit-content
    margin auto
  &.info
    background-color $col_dark_gray
  &.warning
    background-color $col_yellow
  &.error
    background-color $col_red
</style>
