<template>
  <div class="uc-long-text-editor uc-editor" :class="{compact}">
    <uc-text-area v-if="!compact"
            :value="definition.value"
            :label="computedLabel"
            :placeholder="definition.placeholder"
            :readonly="definition.readonly"
            :error="definition.error"
            :tooltip="definition.description"
            @blur="onBlur"
            @input="onInput" />
    <feedback v-for="error in definition.errors" :value="error" :key="error" type="error"></feedback>
    <div v-if="compact" class="truncate">
      <span :title="definition.value">{{definition.value}}</span>
    </div>
  </div>
</template>

<script>
import UcTextArea from '../forms/uc-text-area'
import feedback from '../widgets/field-feedback'
import createSaveCommand from '../../js/create-save-command'
import debounce from 'debounce'

const emitChange = function (newValue) {
  if (this.definition.value !== newValue) {
    const saveCommand = createSaveCommand(this.name, this.definition.guid, this.definition.label, newValue, this.definition.value)
    this.$emit('change', saveCommand)
  }
}

export default {
  name: 'long-text',
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {}
      }
    },
    compact: {
      type: Boolean,
      default: false
    },
    language: String
  },
  computed: {
    computedLabel () {
      const lang = this.definition.multilingual && this.language !== undefined && this.language !== '' ? `(${this.language})` : ''
      return `${this.definition.label} ${lang}`
    }
  },
  components: {
    UcTextArea,
    feedback
  },
  methods: {
    onInput (newValue) {
      this.emitChangeDebounced(newValue)
    },
    onBlur (e) {
      this.emitChange(e.target.value)
    },
    emitChange: emitChange,
    emitChangeDebounced: debounce(emitChange, 400)
  }
}
</script>
<style lang="stylus" scoped>
.uc-long-text-editor
  width 100%
.uc-editor
  margin-top 15px
.compact
  margin-top 0
</style>
