<template>
  <transition name="dropdown">
    <div class="container" v-if="shown">
      <a class="beta">BETA</a>
      <div class="message"><slot></slot></div>
      <a class="feedback" href="mailto:jrm@ucommerce.net" target="_blank">Feedback</a>
      <button class="close" @click="hide"></button>
    </div>
  </transition>
</template>

<script>
import { localStore } from '../../js/storage'
export default {
  name: 'beta-banner',
  data () {
    let hidden = localStore.get('hideBetaBanner')
    return {
      shown: !hidden
    }
  },
  methods: {
    hide () {
      localStore.set('hideBetaBanner', true)
      this.shown = false
    }
  }
}
</script>

<style lang="stylus" scoped>
.container
  height 80px
  background-color $ucommerce-blue
  color #fff
  width 100%
  font-size 12px
  padding 22px 0
  display grid
  grid-template-columns 120px auto 150px 108px
  justify-items start
  place-items center
  .beta
    justify-self center
    font-size 20px
    font-weight bold
  .message
    line-height 1.33
    max-height 36px
  .feedback
    width 101px
    height 34px
    font-size 14px
    font-weight 600
    border-radius 17px
    border solid 1px rgba(255, 255, 255, 0.3)
    display flex
    align-items center
    justify-content center
    color #fff
    cursor pointer
    text-decoration none
  .close
    width 24px
    height 24px
    border-radius: 20px
    font-size 14px
    border solid 1px rgba(255, 255, 255, 0.3)
    background-color transparent
    cursor pointer
    color #fff
</style>
