<template>
  <div class="uc-price-editor uc-editor" :class="{compact}">
    <template v-if="compact">
      <label>{{ price }}</label>
    </template>
    <template v-else>
      <uc-text-input v-show="!editing"
        :value="price"
        :label="computedLabel"
        :error="definition.error"
        :placeholder="definition.placeholder"
        :readonly="definition.readonly"
        :tooltip="definition.description"
        :size="size"
        @focus="onFocus"/>
      <uc-numeric-input v-show="editing"
        :value="definition.value"
        :label="computedLabel"
        :error="definition.error"
        :placeholder="definition.placeholder"
        :readonly="definition.readonly"
        :tooltip="definition.description"
        @blur="onBlur"
        @input="onInput"/>
      <feedback v-for="error in definition.errors" :value="error" :key="error" type="error"/>
    </template>
  </div>
</template>

<script>
import UcTextInput from '../forms/uc-text-input'
import UcNumericInput from '../forms/uc-numeric-input'
import feedback from '../widgets/field-feedback'
import createSaveCommand from '../../js/create-save-command'
import debounce from 'debounce'
import { mapGetters } from 'vuex'

const emitChange = function (newValue) {
  if (this.definition.value !== newValue) {
    const saveCommand = createSaveCommand(this.name, this.definition.guid, this.definition.label, newValue, this.definition.value)
    this.$emit('change', saveCommand)
  }
}

export default {
  name: 'price',
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {}
      }
    },
    compact: {
      type: Boolean,
      default: false
    },
    language: String,
    size: {
      type: String,
      default: () => 'small'
    }
  },
  components: {
    UcTextInput,
    UcNumericInput,
    feedback
  },
  data () {
    return {
      editing: false
    }
  },
  computed: {
    ...mapGetters(['uiLanguage']),
    price () {
      if (!this.definition.value) {
        return ''
      }
      if (this.definition.currencyIsoCode) {
        return this.formattedPrice
      } else {
        return this.definition.value
      }
    },
    formattedPrice () {
      return new Intl.NumberFormat(
        this.uiLanguage, { style: 'currency', currency: this.definition.currencyIsoCode }
      ).format(this.definition.value)
    },
    computedLabel () {
      const lang = this.definition.multilingual ? `(${this.language})` : ''
      return `${this.definition.label} ${lang}`
    }
  },
  methods: {
    onFocus () {
      this.editing = true
    },
    onInput (newValue) {
      this.emitChangeDebounced(newValue)
    },
    onBlur (e) {
      this.emitChange(e.target.value)
      this.editing = false
    },
    emitChange: emitChange,
    emitChangeDebounced: debounce(emitChange, 400)
  }
}
</script>

<style lang="stylus" scoped>
.uc-editor
  margin-top 15px
.compact
  margin-top 0
</style>
