<template>
  <div ref="node" tabindex="0"
  :id="cat.guid"
  class="cat-builder__node"
  :class='classes'
  draggable="true"
  @click='clickHandler'
  @dragstart="dragstartHandler"
  @dragend="dragendHandler"
  @dragenter="dragenterHandler"
  @dragleave="dragleaveHandler"
  @dragover="dragoverHandler"
  @drop.prevent.stop="dropHandler"
  >
    <span v-truncate>{{cat.name}}</span>
    <arrow-forward v-show='cat.hasChildren'></arrow-forward>
  </div>
</template>

<script>
import * as nodeTypes from '../../js/tree-node-types'
import ArrowForward from '../../../shared/icons/arrow-forward-2.vue'
import { createNamespacedHelpers } from 'vuex'
const { mapGetters, mapMutations } = createNamespacedHelpers('builder')
export default {
  name: 'tree-node',
  data () {
    return {
      hoverMsBeforeLoad: 1000,
      dragOverTimestamp: null,
      emitTypes: {
        selected: 'selected',
        changeSection: 'changeSection'
      },
      isDropzone: false,
      dragClone: null
    }
  },
  props: {
    cat: Object,
    catIndex: Number,
    parentGuid: String,
    parentName: String
  },
  computed: {
    ...mapGetters(['dragInfo', 'treeSections']),
    classes () {
      return {
        selected: this.cat.selected,
        displaying: this.cat.displaying,
        dragging: this.dragging,
        dropzone: this.isDropzone
      }
    },
    dragging () {
      return this.dragInfo.dragging && this.dragInfo.node.guid === this.cat.guid
    },
    height () {
      return this.$el.clientHeight
    },
    canDrop () {
      if (!this.dragInfo.dragging) return false
      if (this.cat.guid === this.dragInfo.node.guid) return false // dropping on myself not allowed
      if (this.dragInfo.node.nodeType === nodeTypes.catalog) return false // catalogs can never be dropped on another node
      if (this.dragInfo.node.guid === this.parentGuid) return false // need to make sure we dont create a circular reference
      return true
    }
  },
  components: {
    ArrowForward
  },
  methods: {
    ...mapMutations(['setDragInfo', 'endDrag']),

    clickHandler (e) {
      e.stopPropagation()
      this.setFocus()
      this.select()
    },

    setFocus () {
      this.$el.focus()
    },

    dragstartHandler (e) {
      const dragInfo = {
        dragging: true,
        node: this.cat,
        catIndex: this.catIndex,
        parentGuid: this.parentGuid,
        currentSectionGuid: this.parentGuid
      }
      this.setDragInfo(dragInfo)
      this.dragClone = this.$el.cloneNode(true)
      this.dragClone.classList.add('drag-clone')
      this.dragClone.id = ''
      document.body.appendChild(this.dragClone)
      e.dataTransfer.setData('text', 'anything')
      e.dataTransfer.effectAllowed = 'move'
      e.dataTransfer.setDragImage(this.dragClone, e.offsetX, e.offsetY)
    },

    dragendHandler () {
      this.endDrag()
      document.body.removeChild(this.dragClone)
      this.dragClone = null
    },

    dragenterHandler (e) {
      if (this.canDrop) {
        this.dragOverTimestamp = e.timeStamp
        this.isDropzone = true
      }
    },

    dragleaveHandler () {
      this.isDropzone = false
      this.dragOverTimestamp = null
    },

    dragoverHandler (e) {
      if (this.cat.guid === this.dragInfo.node.guid) return
      if (!this.canDrop) return
      e.preventDefault()
      e.dataTransfer.dropEffect = 'move'
      if ((window.performance.now() - this.dragOverTimestamp) > this.hoverMsBeforeLoad) {
        this.dragOverTimestamp = e.timeStamp
        this.select()
      }
    },

    dropHandler (e) {
      this.isDropzone = false
      if (this.canDrop) {
        const moveTo = {
          parentGuid: this.cat.guid,
          sortOrder: null,
          newIndex: -1,
          catalogGuid: this.cat.nodeType === nodeTypes.catalog ? this.cat.guid : undefined,
          name: this.cat.name || this.parentName
        }
        this.$emit('nodeDropped', moveTo)
      }
    },

    emitDragEvent (dragType, targetIndex) {
      if (this.lastDragEmit !== dragType) {
        this.lastDragEmit = dragType
        this.$emit(dragType, targetIndex)
      }
    },

    select () {
      this.$emit(this.emitTypes.selected, {
        parentGuid: this.parentGuid,
        cat: this.cat
      })
    }
  }
}
</script>

<style lang='stylus' scoped>
.cat-builder__node
  display flex
  justify-content space-between
  align-items center
  width 100%
  height $treenode_height
  border-radius $treenode_border_radius
  padding 2px 10px
  margin-bottom 2px
  font-size 14px
  font-weight normal
  cursor pointer
  outline none
  border 1px dashed rgba(0, 0, 0, 0)
  transition transform 50ms ease, border-color 200ms cubic-bezier(0.600, 1.290, 0.605, -0.525)
  span
    max-width 90%
    display inline-block
    padding 1px 0
    pointer-events none
  svg
    fill $base_text_col
    box(10px)
    pointer-events none
  &.selected, &:hover
    background-color $input-field-stroke
  &.displaying
    background-color $builder-selected-node-bg
    &:focus
      background-color $builder-selected-node-bg
      filter none
  &.dragging
    background-color $platform-bg !important
    background-size 16px 16px
    background-position 0 0 8px 8px
    cursor move
  &.dropzone
    border 1px dashed rgba(0, 0, 0, 0.5)
  &.recent-drop
    background-color yellow
.drag-clone
  background-image none !important
  background none !important
  background-color $builder-selected-node-bg !important
  position absolute
  width 300px
  left -9999px
</style>
