<template>
  <popup-dialog @hideDialog="hideDialog">
    <template v-slot:title>
      {{header}}
    </template>
    <template v-slot:content>
      <component ref="editor"
        :is="definition.editor"
        :language="contentLanguage"
        :definition="definition"
        :name="name"
        @onInput="captureSaveCommand"></component>
    </template>
    <template v-slot:actions>
      <uc-button class="submit-button" :text="$t('global.done')" :type="'submit'" :primary="true" v-on:keyup.enter="captureSaveCommand" @click="submitCommand"></uc-button>
    </template>
  </popup-dialog>
</template>

<script>
import PopupDialog from '../../../shared/components/widgets/popup-dialog'
import Editors from '../../../shared/components/editors'
import { createNamespacedHelpers, mapActions, mapGetters } from 'vuex'
import UcButton from '../../../shared/components/forms/uc-button'

const detailsStore = createNamespacedHelpers('details')
const builderStore = createNamespacedHelpers('builder')

export default {
  name: 'property-editor-dialog',
  components: {
    PopupDialog,
    UcButton,
    ...Editors
  },
  data () {
    return {
      saveCommand: undefined
    }
  },
  props: {
    definition: {
      type: Object,
      default () {
        return {}
      }
    },
    name: {
      type: String,
      default: ''
    },
    customHeader: {
      type: String,
      default: undefined
    }
  },
  mounted () {
    this.$el.addEventListener('keyup', (event) => {
      if (event.keyCode === 13 && this.saveCommand) {
        this.submitCommand()
      }
    })
    this.$nextTick(function () {
      this.focusInput()
    })
  },
  computed: {
    ...mapGetters(['contentLanguage']),
    ...detailsStore.mapGetters(['details', 'validationErrors']),
    header () {
      return this.customHeader ? this.customHeader : `${this.$t('global.change')} ${this.name}`
    }
  },
  methods: {
    ...detailsStore.mapActions(['changeDefinition']),
    ...builderStore.mapMutations(['setCurrentNodeName']),
    ...mapActions(['save']),
    focusInput () {
      if (this.$refs.editor && this.$refs.editor.$el.querySelectorAll('input')[0]) {
        this.$refs.editor.$el.querySelectorAll('input')[0].focus()
      }
    },
    hideDialog () {
      this.$emit('hideDialog')
    },
    captureSaveCommand (saveCommand) {
      this.saveCommand = saveCommand
    },
    async submitCommand () {
      await this.saveChange(this.saveCommand)
      if (this.details['name'].errors) {
        this.focusInput()
        return
      }
      if (this.saveCommand.save.hasOwnProperty('name')) {
        this.setCurrentNodeName(this.saveCommand.save.name.value)
      }
      this.hideDialog()
    },
    async saveChange (command) {
      this.enrichCommand(command)
      await this.save(command)
    },
    enrichCommand (command) {
      const { meta } = command
      meta.contentLanguage = this.contentLanguage
      meta.nodeType = this.details.meta.nodeType
      meta.nodeGuid = this.details.meta.entityGuid
    }
  }
}
</script>

<style scoped lang="stylus">

</style>
