<template>
  <div class="search_container">
    <div class="icon_container" @click="focusInput"><search-icon/></div>
    <uc-text-input ref="searchInput" class="search-input" :placeholder="$t('global.search')" :value="searchTerm" @input="onSearch"/>
    <div class="icon_container close-icon" @click="onClear"><close-icon/></div>
  </div>
</template>

<script>
import UcTextInput from './uc-text-input'
import SearchIcon from '../../icons/search'
import CloseIcon from '../../icons/close'

export default {
  name: 'uc-search',
  components: {
    UcTextInput,
    SearchIcon,
    CloseIcon
  },
  data () {
    return {
      searchTerm: ''
    }
  },
  methods: {
    onSearch (searchTerm) {
      this.searchTerm = searchTerm
      this.$emit('onSearch', searchTerm)
    },
    onClear () {
      this.onSearch('')
      this.focusInput()
    },
    focusInput () {
      this.$refs.searchInput.focus()
    }
  }
}
</script>

<style lang="stylus" scoped>
$space = 8px
$size = 33px

.search_container
  position relative

.search-input /deep/ .uc-text-input__input
  width 100%
  height $size
  padding-left $size + $space + 2

.icon_container
  box(18px)
  width $size
  height $size
  padding $space
  opacity 0.5
  cursor pointer
  position absolute

.close-icon
  top 0px
  right 0px
</style>
