<template>
  <div class="store-selector" v-if="stores.length > 1">
    <v-popover :offset="18" width="200px" :open="popoverOpen" @hide="popoverOpen = false" @apply-show="popoverOpen = true">
      <div class="pill-selected pill tooltip-target">
        <span class="text">{{ selectedStoreLabel }}</span>
        <arrow-down class="arrow-down-icon"></arrow-down>
      </div>
      <template slot="popover">
        <div class="store-dropdown-container">
          <div v-for="store in stores" :key="store.value" @click="setSelectedStore(store); popoverOpen = false" class="store-option">
            <span class="text" :title="store.label">{{store.label}}</span>
          </div>
        </div>
      </template>
    </v-popover>
  </div>

</template>

<script>
import MapStores from '../../js/map-get-stores-response'
import { createNamespacedHelpers } from 'vuex'
import ArrowDown from '../../../shared/icons/triangle-down-filled'
const { mapGetters, mapMutations } = createNamespacedHelpers('builder')

export default {
  name: 'store-selector',
  components: {
    ArrowDown
  },
  data () {
    return {
      popoverOpen: false
    }
  },
  computed: {
    ...mapGetters(['selectedStore', 'stores']),
    selectedStoreLabel () {
      if (this.selectedStore) {
        return this.stores.find(x => x.guid === this.selectedStore).label
      } else {
        return this.$t('global.selectNoOptionsPlaceholder')
      }
    }
  },
  methods: {
    ...mapMutations(['setSelectedStore', 'setStores']),
    async getStores () {
      const res = await this.axios.get('/stores')
      const mappedStores = MapStores(res.data)
      this.setStores(mappedStores)
      if (!this.stores.length) return
      this.setSelectedStore(this.stores[0])
    }
  },
  created () {
    this.getStores()
  }
}
</script>

<style lang="stylus">
  .store-selector
    margin-bottom 12px
  .store-option .text::after
    content attr(title)
    font-weight bold
    height 0
    overflow hidden
    visibility hidden
    display block

  .store-dropdown-container
    display flex
    color $primary-text
    flex-direction column
    height 100%

  .pill
    display flex
    height 24px
    flex-grow 1
    align-items center
    border-radius 13px
    primary-details-text()
    justify-content space-between
    padding 2px 14px 2px 14px
    cursor pointer

  .store-option
    display flex
    height 24px
    flex-grow 1
    align-items center
    primary-details-text()
    justify-content space-between
    cursor pointer
    &:hover
      primary-details-text-bold()

  .store-option + .store-option
    margin-top 20px

  .pill.pill-selected
    background-color $ucommerce-light-blue
    primary-details-text-bold()

  .pill + .pill
    margin-left 17px

  .arrow-down-icon
    width 12px
    height 13px
    object-fit contain
    fill $primary-text
    stroke $primary-text
    margin-left 4px

  .selected
    primary-details-text-bold()
  .single-store-selector
    max-width 200px
    display inline-block
    cursor default
</style>
