<template>
  <div class="preview-bar wrapper">
    <div class="preview-bar container">
      <label>{{ selectedItemsSummary }}</label>
      <button class="preview-bar save btn primary" @click="save">{{this.$t('global.done')}}</button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'content-selector-preview-bar',
  computed: {
    ...mapGetters('navigator', ['selectedItems']),
    selectedItemsSummary () {
      const count = this.selectedItems.length
      return this.$tc('contentNavigator.itemsSelected', count)
    }
  },
  methods: {
    save () {
      this.$emit('save', this.selectedItems)
    }
  }
}
</script>

<style lang="stylus" scoped>
.preview-bar
  position relative
  &.wrapper
    width 100%
    height 60px
    background-color #ffffff
  &.container
    width 100%
    height 100%
    border-top solid 1px $col_light_gray
    center()
  &.save
    height 100%
    position absolute
    right 0
</style>
