<template>
  <div class="uc-multi-select">
    <template v-if="compact">
      <div :title="fullDescription" class="compact description">{{description}}</div>
    </template>
    <template v-else>
      <div v-if="definition.options.length" class="expanded">
        <label class="uc-input-label">{{ definition.label }}</label>
        <div v-if="longList()">
          <uc-search @onSearch="onSearch"/>
          <div v-if="!matchesFound()" class="uc-search_no-results long-list">{{ $t('global.searchNoResults') }}</div>
        </div>
        <div v-if="matchesFound()" class="uc-input-wrapper" :class="{ 'uc-multi-select__list long-list': longList() }">
          <uc-checkbox class="uc-multi-select__check" v-for="option in filteredOptions" :readonly="definition.readonly"
            :guid="option.guid" :key="option.guid"
            :label="option.label" :value="option.guid"
            :checked="isChecked(option.guid)" @change="change"/>
        </div>
        <feedback v-for="error in definition.errors" :value="error" :key="error" type="error"></feedback>
      </div>
    </template>
  </div>
</template>

<script>
import UcCheckbox from '../forms/uc-checkbox'
import UcSearch from '../forms/uc-search'
import createSaveCommand from '../../js/create-save-command'
import deepCopy from '../../js/deep-copy'
import feedback from '../widgets/field-feedback'

export default {
  name: 'multi-select',
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {
          options: []
        }
      }
    },
    compact: {
      type: Boolean,
      default: false
    }
  },
  components: {
    UcCheckbox,
    UcSearch,
    feedback
  },
  data () {
    return {
      searchTerm: ''
    }
  },
  computed: {
    filteredOptions () {
      if (this.searchTerm === '' || this.searchTerm === null) {
        return this.definition.options
      } else {
        return this.definition.options.filter(option => option.label.toLowerCase().includes(this.searchTerm.toLowerCase()))
      }
    },
    description () {
      if (!this.definition.options || !this.definition.options.length) return ''
      if (!this.definition.selected) return ''
      let options = this.definition.options.filter(option => this.definition.selected.includes(option.guid))
      if (!options.length) return ''
      if (options.length === 1) return options[0].label
      return `${options[0].label} and ${options.length - 1} more`
    },
    fullDescription () {
      if (!this.definition.options || !this.definition.options.length || !this.definition.selected) return ''
      let options = this.definition.options.filter(option => this.definition.selected.includes(option.guid))
      if (!options.length) return ''
      return options.map(option => option.label).join(', ')
    }
  },
  methods: {
    isChecked (optionGuid) {
      if (this.definition.selected) {
        return this.definition.selected.includes(optionGuid)
      }
      return false
    },
    longList () {
      return this.definition.options.length > 6
    },
    matchesFound () {
      return this.filteredOptions.length > 0
    },
    change (e) {
      let newValue = deepCopy(this.definition.selected ? this.definition.selected : [])
      if (e.value) {
        newValue.push(e.guid)
      } else {
        newValue = newValue.filter((guid) => guid !== e.guid)
      }

      const saveCommand = createSaveCommand(this.name, this.definition.guid, this.definition.label, newValue, this.definition.selected ? this.definition.selected : [], 'selected')
      this.$emit('change', saveCommand)
    },
    onSearch (searchTerm) {
      this.searchTerm = searchTerm
    }
  }
}
</script>

<style lang="stylus" scoped>
$checkbox_height = 18px
$space = 8px
$item_height = $checkbox_height + $space
$long-list_height = $item_height * 6 + 6

.uc-multi-select
  .compact.description
    white-space nowrap
    overflow hidden
    text-overflow ellipsis
  .expanded
    min-width 300px
    max-width 500px
.uc-multi-select__list
  white-space wrap
  padding $space
  overflow-y auto

.long-list
  height $long-list_height
  border 1px solid $col_light_border
  border-top none

.uc-multi-select__check:not(:last-child)
  margin-bottom $space

.uc-search_no-results
  font-size 1.5em
  text-align center
  line-height $long-list_height
  color $col_light_border

.uc-input-label
  display block

.uc-multi-select__tooltip
  margin-left 5px
.uc-input-wrapper
  margin-top 16px
</style>
