<template>
  <transition-group name="list-left" tag="div" class="uc-undo-list">
    <!-- eslint-disable vue/valid-next-line vue/no-unused-vars -->
    <div v-for="(undos, key, index) in groups" :key="key" class="uc-undo" :class="{ latest: isLatest(groups, index) }">
      <span v-html="caption(undos)"></span>
      <button class="btn uc-undo__btn"  @click="undoLast(undos)">{{buttonCaption(undos)}}</button>
    </div>
  </transition-group>
</template>

<script>

import customUndo from './../../../CatalogFoundation/translations/components/custom-undo'

const groupBy = function (xs, key) {
  return xs.reduce(function (rv, x) {
    (rv[x[key]] = rv[x[key]] || []).push(x)
    return rv
  }, {})
}

export default {
  name: 'undo',
  props: {
    undos: Array
  },
  computed: {
    groups () {
      return groupBy(this.undos, 'key')
    }
  },
  methods: {
    caption (undos) {
      const message = undos[undos.length - 1].meta.undoMessage
      if (message) {
        return message
      }
      const label = undos[undos.length - 1].meta.label
      const labelPretty = label[0].toUpperCase() + label.slice(1)
      const nodeType = undos[undos.length - 1].meta.nodeType
      const nodeTypePretty = nodeType.replace(/([A-Z])/g, ' $1')
      const locale = this.$i18n.locale
      const key = label.replace(/\s/g, '').toLowerCase()

      if (customUndo[locale][key] !== undefined) {
        return this.$tc(`customUndo.${key}`, undos.length, { label: labelPretty, count: undos.length, nodeType: nodeTypePretty })
      } else {
        return this.$tc('global.undoCaption', undos.length, { label: labelPretty, count: undos.length, nodeType: nodeTypePretty })
      }
    },
    isLatest (groups, index) {
      return index === Object.getOwnPropertyNames(groups).length - 1
    },
    buttonCaption (undos) {
      return this.$tc('global.undo', undos.length)
    },
    undoLast (undos) {
      this.$emit('undo', undos[undos.length - 1])
    }
  }
}
</script>

<style lang="stylus">
.uc-undo-list
  z-index 9999
  position fixed
  bottom 60px
  right 20px
  display flex
  flex-direction column
  align-items flex-end

.uc-undo
  padding 6px 6px 6px 12px
  color #fff
  background-color $col_black
  box-shadow 2px 2px 7px rgba(0,0,0,0.3)
  border-radius 3px
  margin-bottom 4px
  font-size 0.875rem
  opacity 0.75
  &.latest
    opacity 1.0

.btn.uc-undo__btn
  margin-left 30px
  padding 0.3em
  height 2em
  border-radius 2px
</style>
