const getters = {
  systemInfo: state => state.systemInfo,
  languages (state) {
    if (state.systemInfo.languages.length === 0) return state.systemInfo.languages
    return state.systemInfo.languages.map(x => {
      return {
        text: x.name,
        value: x.cultureCode
      }
    })
  },
  uiLanguage: state => state.uiLanguage,
  contentLanguage: state => state.contentLanguage,
  undos: state => state.undos,
  errorMessage: state => state.errorMessage,
  definitionsByDefinitionGuid: state => state.definitionsByDefinitionGuid
}

export default getters
