<template>
  <div class="details-catalog">
    <h2 class="basic-info-heading">Basic Information</h2>
    <section class="details-catelog__static">
      <div class="details-catelog__static-right">
          <uc-boolean :definition="details.displayOnWebsite" class="compact" name="displayOnWebsite" @change="save" />
          <uc-boolean :definition="details.showPricesIncludingVAT" class="compact" :name="'showPricesIncludingVAT'" @change="save" />
          <uc-short-text v-if="language" :showLanguage="true" :language="language" :definition="details.displayName" name="displayName" @change="save" />
          <uc-short-text v-if="language" :showLanguage="true" :language="language" :definition="details.name" name="name" @change="save" />
          <uc-single-select :definition="details.defaultPriceGroup" :name="'defaultPriceGroup'" @change="save" />
          <uc-multi-select :definition="details.allowedPriceGroups" :name="'allowedPriceGroups'" @change="save" />
      </div>
    </section>
  </div>
</template>

<script>
import UcSingleSelect from '../../../shared/components/editors/uc-single-select'
import UcShortText from '../../../shared/components/editors/uc-short-text'
import UcBoolean from '../../../shared/components/editors/uc-boolean'
import UcMultiSelect from '../../../shared/components/editors/uc-multi-select'
import { createNamespacedHelpers, mapGetters as globalGetters } from 'vuex'
const { mapGetters } = createNamespacedHelpers('details')
export default {
  name: 'catalog-details',
  props: {
    language: String
  },
  computed: {
    ...mapGetters(['details']),
    ...globalGetters(['systemInfo'])
  },
  consumedProperties: ['displayName', 'displayOnWebsite', 'showPricesIncludingVAT', 'defaultPriceGroup', 'allowedPriceGroups', 'definition'],
  components: {
    UcSingleSelect,
    UcMultiSelect,
    UcShortText,
    UcBoolean
  },
  methods: {
    save (e) {
      this.$emit('change', e)
    }
  }
}

</script>

<style lang="stylus" scoped>
.basic-info-heading
  primary-heading-2()
  margin: 0 0 30px 0
.details-catelog__static
  display flex
  align-items flex-start
  justify-content space-between
.details-catelog__static-right
  width: 100%
  display grid
  grid-template-rows 3
  grid-gap 20px
.compact
  margin-bottom 0
</style>
