<template>
  <div class="create-container" v-on:keyup.esc="requestHideInput">
    <div class="create-overlay" @click.prevent="requestHideInput"></div>
    <div class="create-dialog" @click.stop="">
      <header>{{ title }}</header>
      <form class="create-form" @submit.prevent="createNewNode">

        <uc-text-input ref="input" :placeholder="placeholder" @input="updateName" :value="newName" :label="label" />

        <div class="create-error"><span v-show="errorMessage.length">{{ errorMessage }}</span></div>
        <div class="create-actions">
          <uc-button :text="createLabel" :type="'submit'" :primary="true" />
          <uc-button :text="this.$t('global.cancel')" :primary="false" @click="requestHideInput" />
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { nodeTypeApiMap, nodeTypeSingular, catalog } from '../../js/tree-node-types'
import UcTextInput from '../../../shared/components/forms/uc-text-input'
import UcButton from '../../../shared/components/forms/uc-button'
import { createNamespacedHelpers } from 'vuex'
const { mapGetters } = createNamespacedHelpers('builder')
export default {
  name: 'create-node',
  data () {
    return {
      newName: '',
      errorMessage: '',
      label: ''
    }
  },
  props: {
    nodeType: String,
    parentInfo: Object,
    inputVisible: Boolean
  },
  mounted () {
    this.focusInput()
  },
  components: {
    UcTextInput,
    UcButton
  },
  computed: {
    ...mapGetters(['selectedStore']),
    title () {
      return `${this.$t('createNode.title', {
        nodeType: this.nodeTypeSingularCapitalized
      })}`
    },
    nodeTypeSingularCapitalized () {
      return `${nodeTypeSingular[this.nodeType].substr(0, 1).toUpperCase() + nodeTypeSingular[this.nodeType].substr(1)}`
    },
    placeholder () {
      return `${this.nodeTypeSingularCapitalized} ${this.$t('global.name')}`
    },
    createLabel () {
      return `${this.$t('global.create')} ${this.nodeTypeSingularCapitalized}`
    }
  },
  methods: {
    requestShowInput () {
      this.$emit('requestShowInput')
    },
    requestHideInput () {
      this.$emit('requestHideInput')
    },
    focusInput () {
      this.$refs.input.setActiveFocus()
    },
    updateName (name) {
      this.newName = name
      this.errorMessage = ''
    },
    createNewNode () {
      if (!this.newName.trim().length) {
        this.errorMessage = `${this.$t('createNode.emptyNotAllowed', { field: this.placeholder })}`
        this.focusInput()
        return
      }
      const payload = {
        ...this.parentInfo,
        sortOrder: 0,
        name: this.newName
      }
      if (this.nodeType === catalog) {
        payload.storeGuid = this.selectedStore
      }

      this.axios.post(`${nodeTypeApiMap[this.nodeType]}`, payload)
        .then(res => {
          this.newName = ''
          this.$emit('nodeCreated', { isCatalog: this.nodeType === catalog, parentGuid: this.parentInfo.parentGuid })
        })
        .catch(err => {
          if (err.response.status === 400) {
            this.errorMessage = err.response.data.Message
          }
          this.focusInput()
        })
    }
  }
}
</script>

<style lang="stylus" scoped>
.create-container
  box(100%)
  position fixed
  top 0
  left 0
  z-index 5000

.create-overlay
  box(100%)
  position fixed
  background #000
  opacity 0.4
  cursor pointer

.create-dialog
  position absolute
  top 50%
  left 50%
  transform translate(-50%,-50%)
  box(600px, 460px)
  padding 62px 115px
  background-color #fff
  box-shadow 0 4px 30px 0 rgba(0, 0, 0, 0.15)

header
  primary-heading-1()
  text-align center

.create-form
  box(100%, auto)
  margin-top 60px
  display flex
  flex-direction column
  justify-content space-between
  align-items flex-start
  // deep selector shall be removed when editors are fixed - Understood? - When are those finished? And why are they there in the first place. Asking for a friend?.
  /deep/ .uc-text-input,
  /deep/ .uc-text-input__input
    box(100%, 50px)

.create-error
  box(100%, 50px)
  color $col_red
  line-height 12px
  font-size 12px
  padding 7px 16px
  white-space normal

.create-actions
  width 100%
  display flex
  flex-direction column
  align-items center
  .uc-button
    box(200px, 48px)
  .uc-button + .uc-button
    margin-top 15px
</style>
