<template>
  <div>
    <uc-label class="uc-image-picker__label" :definition="definition" :language="language" v-if="withLabel"></uc-label>
  <div class="picker-container" :class="{ compact: compact, full: !compact }">
    <div v-if="definition" class="stretch">
      <section class="stretch media-area" @mouseover="hovered = true" @mouseleave="hovered = false" @click.prevent="toggle()">
        <div  class="stretch nothing-selected" v-show="count === 0 && !definition.readonly">
          <div class="stretch nothing-selected inner">+</div>
        </div>
        <uc-image class="stretch" v-if="count > 0" :readonly="definition.readonly" :src="definition.mediaUrl" :alt="definition.mediaName"/>
        <label class="name" v-if="!definition.mediaUrl">{{ definition.mediaName }}</label>
        <template v-if="!this.definition.readonly">
          <div v-show="hovered" class="actions">
            <div v-show="count > 1" class="icon label background" >{{ countSummary }}</div>
            <div v-if="count > 0" class="icon small background edit"><edit-icon class="icon-edit"/></div>
            <div @click.prevent.stop="reset()" v-if="count > 0" class="icon small background" ><close-icon class="icon-close align-right"/></div>
          </div>
        </template>
      </section>
    </div>
    <div v-if="toggled" class="stretch picker dialog">
      <header class="picker header-section">
        <div v-truncate class="picker header-label">{{ definition.label }}</div>
        <div class="icon big align-right" @click="toggled = !toggled"><close-icon class="icon-close"/></div>
      </header>
      <content-navigator class="padding-x" :dataUrl="contentType"/>
      <content-selector class="picker selector padding-x" :multiSelect="multiSelect" :preSelectedItems="definition.selected"/>
      <preview-bar class="picker preview-bar" @save="saveAndClose"/>
    </div>
  </div>
  </div>
</template>

<script>
import UcImage from '../../../shared/components/uc-image'
import UcLabel from '../../../shared/components/forms/uc-label'
import CloseIcon from '../../icons/close'
import EditIcon from '../../icons/edit-pencil'
import ContentNavigator from '../../../CatalogFoundation/components/content-navigator/content-navigator'
import ContentSelector from '../../../CatalogFoundation/components/content-navigator/content-selector'
import PreviewBar from '../../../CatalogFoundation/components/content-navigator/content-selector-preview-bar'
import createSaveCommand from '../../../shared/js/create-save-command'

export default {
  name: 'uc-image-picker',
  components: {
    UcImage,
    CloseIcon,
    EditIcon,
    ContentNavigator,
    ContentSelector,
    PreviewBar,
    UcLabel
  },
  props: {
    name: String,
    definition: Object,
    compact: {
      type: Boolean,
      default: false
    },
    multiSelect: Boolean,
    contentType: String,
    language: String,
    withLabel: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      toggled: false,
      hovered: false,
      lastScrollPosition: Number
    }
  },
  computed: {
    count () {
      return (this.definition.selected || []).length
    },
    countSummary () {
      return `+ ${this.count - 1} more`
    }
  },
  watch: {
    toggled () {
      if (this.toggled) {
        this.lastScrollPosition = document.documentElement.scrollTop
        document.documentElement.style.overflow = 'hidden'
        document.addEventListener('keydown', this.keydownHandler)
      } else {
        document.documentElement.style.overflow = 'auto'
        document.removeEventListener('keydown', this.keydownHandler)
        document.documentElement.scrollTop = this.lastScrollPosition
      }
    }
  },
  methods: {
    toggle () {
      if (this.definition.readonly) return
      this.toggled = !this.toggled
    },
    reset () {
      this.save([])
    },

    async save (guids) {
      const saveCommand = createSaveCommand(this.name, this.definition.guid, this.definition.label, guids, this.definition.selected || [], 'selected')
      if (guids.length) {
        const content = await this.axios.get(`${this.contentType}/${guids[0]}`)
        let { save } = saveCommand
        save[this.name].mediaName = content.data.name
        save[this.name].mediaUrl = content.data.url
      }
      let { undo } = saveCommand
      undo[this.name].mediaName = this.definition.mediaName
      undo[this.name].mediaUrl = this.definition.mediaUrl
      this.$emit('change', saveCommand)
    },
    saveAndClose (arr) {
      this.save(arr)
      this.toggle()
    },

    keydownHandler (e) {
      if (e.key === 'Escape') {
        this.toggle()
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.picker-container
  &.compact
    box(50px)
  &.full
    box(256px)

$padding = 8px
.media-area
  position relative
  .nothing-selected
    center()
    cursor pointer
    background-color $col_light_bg
    &.inner
      box(80%)
      opacity .3
      font-size 4em
      font-weight 800
      outline 2px dashed black
      &:hover
        opacity .7
  .name
    position absolute
    bottom 20%
    cursor pointer
    fit-label()
  .actions
    position absolute
    display flex
    width 100%
    padding 8px
    bottom 0
    right 0
    cursor pointer

$sizeSmall = 16px
$sizeBig = $sizeSmall * 2
.icon
  center()
  padding $padding
  margin 4px
  cursor pointer
  opacity 0.9
  &.label
    height 100%
    border-radius 24px
  &.small
    circle($sizeSmall)
    box($sizeBig)
  &.big
    circle($sizeBig)
    box($sizeBig * 2)
  &.edit
    margin-left auto
  &.background
    background-color $col_light_border
  &:hover
    background-color $col_light_gray

.icon-edit
  box($sizeBig)
.icon-close
  box($sizeBig * 2)

.picker
  &.dialog
    display flex
    flex-direction column
    position absolute
    top 0
    left 0
    z-index 1000
    background-color #fff
  &.header-section
    display flex
    justify-content space-between
    align-items center
    padding 20px
    padding-bottom 0
  &.header-label
    font-size 1.5em

  &.selector
    margin 32px 0
    padding-top 4px
  &.preview-bar
    margin-top auto

.padding-x
  padding 0 20px
.align-right
  margin-left auto
.stretch
  stretch()

.uc-image-picker__label
  display inline-block

.uc-image-picker__tooltip
  margin-left 5px
</style>
