<template>
  <div class="uc-select" :class="{readonly}">
    <div :id="id" class="uc-select__selected" @click="toggleOptions">{{selectedText}}</div>
    <div class="uc-select__arrow" :class="{toggled: optionsToggled}" @click="toggleOptions"><triangle></triangle></div>
    <transition name="dropdown">
      <ul v-show="showOptions" class="uc-select__options dropdown-overlay">
        <li class="uc-select__option" v-for="option in filteredOptions" :key="option.value" @click="selectOption(option)">{{option.label}}</li>
      </ul>
    </transition>
  </div>
</template>

<script>
import Triangle from '../../icons/triangle-right.vue'
import getID from '../../js/id-generator'
export default {
  name: 'uc-select',
  data () {
    return {
      id: 0,
      optionsToggled: false
    }
  },
  props: {
    options: Array,
    selected: String,
    placeholder: {
      Type: String,
      default: function () {
        return this.$t('global.selectPlaceholder')
      }
    },
    readonly: Boolean
  },
  computed: {
    selectedOption () {
      return this.options ? this.options.find(x => x.guid === this.selected) : ''
    },
    selectedText () {
      return this.selectedOption ? this.selectedOption.label : this.placeholder
    },
    showOptions () {
      return this.optionsToggled && !this.readonly && this.options.length > 0
    },
    filteredOptions () {
      return this.options.filter(option => option !== this.selectedOption)
    }
  },
  methods: {
    toggleOptions (event) {
      if (this.readonly) return
      event.preventDefault()
      this.optionsToggled = !this.optionsToggled
      if (this.optionsToggled) {
        window.setTimeout(() => {
          window.addEventListener('click', this.closeHandler)
        }, 10)
      }
    },
    selectOption (option) {
      if (this.readonly) return
      this.$emit('selected', option)
      this.optionsToggled = false
    },
    closeHandler (event) {
      this.optionsToggled = false
      window.removeEventListener('click', this.closeHandler)
    }
  },
  components: {
    Triangle
  },
  created () {
    this.id = getID()
  }
}
</script>

<style lang="stylus">
.uc-select
  display block
  cursor pointer
  position relative
  &.readonly
    cursor not-allowed

.uc-select__label
  display block

.uc-select__selected
  input-field()
  padding-right 15px
  border-right 1px solid #fff
  white-space nowrap

.uc-select__arrow
  input-field()
  padding-left 10px
  padding-right 10px
  svg
    box(6px)
    transition transform 200ms ease
    transform rotate3d(0, 0, 1, 90deg)

  &.toggled
    svg
      transform rotate3d(0, 0, 1, -90deg)

.uc-select__options
  zero-list()
  display inline-block
  color $base_text_col
  min-width 100%
  z-index 100

.uc-select__option
  white-space nowrap
  padding 8px 15px
  font-size $input_font_size
  cursor pointer
  &:hover
    background-color $col_light_selection

</style>
