<template>
  <div class="uc-boolean-select" :class="{compact}">
    <template v-if="compact">{{ translatedValue }}</template>
    <toggle v-else
      :readonly="definition.readonly"
      :label="definition.label"
      :guid="definition.guid"
      :checked="definition.value"
      :tooltip="definition.description"
      :compact="compact"
      @change="change"></toggle>
  </div>
</template>

<script>
import Toggle from '../forms/uc-toggle'
import CreateSaveCommand from '../../js/create-save-command'
export default {
  name: 'boolean',
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {}
      }
    },
    compact: {
      type: Boolean,
      default: false
    }
  },
  components: {
    Toggle
  },
  computed: {
    translatedValue () {
      const answer = (this.definition.value) ? 'yes' : 'no'
      return this.$t(`global.${answer}`)
    }
  },
  methods: {
    change (e) {
      const command = CreateSaveCommand(this.name, this.definition.guid, this.definition.label, e.value, this.definition.value)
      this.$emit('change', command)
    }
  }
}
</script>

<style lang="stylus" scoped>
  .uc-boolean-select
    margin-top 30px
  .compact
    margin-top 0
</style>
