import * as types from './mutation-types'
import { nodeTypeApiMap } from '../../js/tree-node-types'
import { mergeAllData } from '../../js/merge-definitions-and-data'

const actions = {
  async changeContentLanguage ({ commit, state, rootState }, contentLanguage) {
    if (!this.details && !this.details.meta) {
      return
    }

    // Language was changed, so we request product details for the product in the new contentLanguage.
    const details = await this._vm.axios.get(`${nodeTypeApiMap[state.details.meta.nodeType]}/${state.details.meta.entityGuid}/${contentLanguage}`)

    // Temporarily in place due to us only merging data + metadata in case of a product currently.
    if (state.details.meta.nodeType === 'product') {
      const mergedObject = mergeAllData(rootState.definitionsByDefinitionGuid, [details.data])

      // Save the details to VueX
      commit(types.SET_DETAILS, mergedObject[0])
    } else {
      commit(types.SET_DETAILS, details.data)
    }
  }
}

export default actions
