const folderNodeTypes = [
  'album',
  'folder',
  'mediaLibrary',
  'mediaLibraryFolder',
  'content'
].map(x => x.toLowerCase())

export default function (arr) {
  return arr.map(media => {
    media.guid = media.id
    if (folderNodeTypes.includes(media.nodeType.toLowerCase())) {
      media.pickerNodeType = 'Folder'
    } else {
      media.pickerNodeType = media.nodeType
    }
    return media
  })
}
