<template>
  <router-view/>
</template>

<script>
import Themes from '../shared/js/themes.js'
import { mapMutations } from 'vuex'
export default {
  name: 'root-app',
  async created () {
    const response = await this.axios.get('system')
    const systemInfo = response.data
    this.applyCMSTheme(systemInfo.cms)
    this.setSystemInfo(systemInfo)
    this.setContentLanguage(systemInfo.languages.length ? systemInfo.languages[0].cultureCode : null)
    this.setUiLanguage(systemInfo.currentCultureCode ? systemInfo.currentCultureCode.substr(0, 2) : null)
    this.$i18n.locale = systemInfo.currentCultureCode.substr(0, 2)
  },
  methods: {
    ...mapMutations(['setSystemInfo', 'setContentLanguage', 'setUiLanguage']),
    applyCMSTheme (cmsName) {
      const doc = document.documentElement
      const cmsProps = Themes[cmsName]
      Object.keys(cmsProps).forEach(selector => {
        doc.style.setProperty(selector, cmsProps[selector])
      })
    }
  }
}
</script>

<style lang="stylus">
</style>
