<template>
  <div class="uc-numeric-input">
    <label class="uc-numeric-input__label input-label" v-if="label" :for="id" :class="{ error: error }">{{label}}</label>
    <div class="input-field-container">
      <div class="input-field-wrapper">
        <input ref="field" class="uc-numeric-input__input" type="number" :id="id" :value="value" :disabled="readonly" :placeholder="placeholder"
        @input="emitValue"
        @focus="$emit('focus', $event)"
        @blur="$emit('blur', $event)">
      </div>
    </div>
  </div>
</template>

<script>
import getID from '../../js/id-generator'
export default {
  name: 'uc-numeric-input',

  data () {
    return {
      id: null
    }
  },
  props: {
    label: String,
    placeholder: String,
    value: String, // value Number: error served as a string, no time to investigate Anders C
    readonly: Boolean,
    error: String,
    tooltip: String
  },
  methods: {
    emitValue (event) {
      this.$emit('input', event.target.value)
    },
    focus () {
      this.$refs.field.focus()
    }
  },
  created () {
    this.id = getID()
  }
}
</script>

<style lang="stylus" scoped>

  .uc-editor
    label
      font-size: 16px
      position absolute
      line-height 20px
      left 14px
      padding 0 4px
      background-color #fff
      font-weight normal
      top 17px
      removeUserSelect()

  &.active
    label
      transform translateY(-28px)
      transition-duration 200ms
      font-size 12px

  .uc-numeric-input
    position relative
  .uc-numeric-input__input
    input-field()
    width 100%
    padding: 0 16px 0 16px
    height: 50px;
    font-size 16px
    line-height 1.6
    display block
    &:disabled
      cursor not-allowed
  .uc-numeric-input__label
    display inline-block
  .uc-numeric-input__tooltip
    margin-left 5px
  .error
    color $col_red

  .input-field-container

    border-radius 4px
    background-color #e1e1e8
    .input-field-wrapper
      border-radius 4px
      background-color #fff
      border 1px #e1e1e8 solid
      overflow hidden

    input[type=number]::-webkit-inner-spin-button,
    input[type=number]::-webkit-outer-spin-button
      appearance none
      margin 0
</style>
