<template>
  <div class="uc-date-picker uc-editor" :class="{compact}">
    <template v-if="compact && definition.value">
      <label>{{ formatter(definition.value) }}</label>
    </template>
    <template v-if="!compact">
      <label class="input-label" v-if="definition.label" :class="{ error: definition.error }">{{definition.label}}</label>
      <div class="input-field-container">
        <div class="input-field-wrapper">
          <div class="uc-datepicker">
            <label class="input-label" v-if="label">{{label}}</label>
            <date-picker class="date-picker"
              :disabled="definition.readonly"
              :value="definition.value"
              :format="formatter"
              :placeholder="definition.label"
              :full-month-name="true"
              :calendar-class="'calendar'"
              :wrapper-class="'wrapper'"
              input-class="datepicker-input"
              @selected="selected"/>
          </div>
        </div>
      </div>
      <feedback v-for="error in definition.errors" :value="error" :key="error" type="error"/>
    </template>
  </div>
</template>

<script>
import DatePicker from 'vuejs-datepicker'
import { Format, locales } from '../../js/date-formatter'
import CreateSaveCommand from '../../js/create-save-command'
import feedback from '../widgets/field-feedback'
export default {
  name: 'uc-date-picker',
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {}
      }
    },
    compact: {
      type: Boolean,
      default: false
    },
    format: {
      type: String,
      default: 'DD. MMMM YYYY'
    },
    locale: {
      type: String,
      default: 'en-US'
    },
    label: String
  },
  components: {
    DatePicker,
    feedback
  },
  methods: {
    selected (date) {
      if (this.definition.value !== date) {
        const command = CreateSaveCommand(this.name, this.definition.guid, this.definition.label, date, this.definition.value)
        this.$emit('change', command)
      }
    },
    formatter (date) {
      return Format(date, this.format, locales[this.locale])
    }
  }
}
</script>
<style lang="stylus">
  .datepicker-input
    width: 100%
    cursor pointer
</style>
<style lang="stylus" scoped>
.error
  color $col_red
.input-label
  display inline-block
  position absolute
  color $ucommerce-secondary
  line-height 12px
  left 14px
  top 20px
  z-index 9
  padding 4px
  background-color #fff
  transition-duration: 200ms
  font-size 12px
  font-weight normal
  top -11px

.uc-date-picker__tooltip
  margin-left 5px

.uc-editor
  position relative
  margin-top 15px
.compact
  margin-top 0

.input-field-container

  border-radius 4px
  background-color #fff

  .input-field-wrapper
    padding 12px
    border-radius 4px
    background-color #fff
    border 1px #e1e1e8 solid

  &.disabled
    background-color #f7f7f9

/* Deep styling the 3rd party libraries datepicker */
.date-picker
  & >>> .cell:not(.blank):not(.disabled).day:hover
  & >>> .cell:not(.blank):not(.disabled).month:hover
  & >>> .cell:not(.blank):not(.disabled).year:hover
    border 1px solid $ucommerce-light-blue
  & >>> .cell.day.selected
  & >>> .cell.month.selected
  & >>> .cell.year.selected
    background $ucommerce-light-blue
  & >>> .calendar
    margin-bottom 20px

</style>
