<template>
  <div>
    <v-popover :offset="18" width="200px" :open="popoverOpen" @hide="popoverOpen = false" @apply-show="popoverOpen = true">
      <div v-if="!fitsSimpleEditor" class="pill-selected pill tooltip-target">
        <span class="text">{{contentLanguageName}}</span>
        <arrow-down class="arrow-down-icon"></arrow-down>
      </div>
      <template slot="popover">
          <div class="language-dropdown-container">
            <div v-for="language in languages" :key="language.value" @click="setContentLanguage(language.value); popoverOpen = false" class="language-option" :class="isSelectedLanguage(language) ? 'selected' : ''">
              <span class="text" :title="language.text">{{language.text}}</span>
            </div>
        </div>
      </template>
    </v-popover>

    <div v-if="optionsCount > 1 && fitsSimpleEditor" class="language-pill-container" ref="pillContainer">
      <div v-for="language in languages" :class="isSelectedLanguage(language) ? 'pill-selected pill' : 'pill'"
           :key="language.value"
           @click="setContentLanguage(language.value)">
        <span class="text" :title="language.text">{{language.text}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import ArrowDown from '../../../shared/icons/triangle-down-filled'
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'language-selector',
  components: {
    ArrowDown
  },
  data () {
    return {
      popoverOpen: false,
      fitsSimpleEditor: {
        type: Boolean,
        default: false
      }
    }
  },
  mounted () {
    this.setFitsSimpleEditor()
  },
  watch: {
    optionsCount: function () {
      this.setFitsSimpleEditor()
    }
  },
  computed: {
    ...mapGetters(['languages', 'contentLanguage']),
    optionsCount () {
      return this.languages.length
    },
    contentLanguageName () {
      return this.languages.find(lang => lang.value === this.contentLanguage).text
    }
  },
  methods: {
    ...mapMutations(['setContentLanguage']),
    setFitsSimpleEditor () {
      this.$nextTick(() => {
        if (this.$refs && this.$refs.pillContainer) {
          let elementsWidth = this.$refs.pillContainer.getBoundingClientRect().width
          this.fitsSimpleEditor = elementsWidth < 400
        }
      })
    },
    isSelectedLanguage (language) {
      return language.value === this.contentLanguage
    }
  }
}
</script>

<style lang="stylus" scoped>
  .language-option,
  .pill
    .text::after
      content attr(title)
      font-weight bold
      height 0
      overflow hidden
      visibility hidden
      display block
  .text
    white-space nowrap

.language-pill-container
   display flex
   max-width 400px
   color $primary-text

  .language-dropdown-container
    display flex
    color $primary-text
    flex-direction column
    height 100%

  .pill
    display flex
    height 24px
    flex-grow 1
    align-items center
    border-radius 13px
    primary-details-text()
    justify-content space-between
    padding 2px 14px 2px 14px
    cursor pointer

  .language-option
    display flex
    height 24px
    flex-grow 1
    align-items center
    primary-details-text()
    justify-content space-between
    cursor pointer
    &:hover
      primary-details-text-bold()

  .language-option + .language-option
    margin-top 20px

  .pill.pill-selected
    background-color $ucommerce-light-blue
    primary-details-text-bold()

  .pill + .pill
    margin-left 17px

  .arrow-down-icon
    width 12px
    height 13px
    object-fit contain
    fill $primary-text
    stroke $primary-text
    margin-left 4px

  .selected
    primary-details-text-bold()

</style>
