export default function (url, key, value) {
  key = encodeURIComponent(key)
  let encodedValue = encodeURIComponent(value)
  // deconstruct url
  let [base, anchor] = url.split('#')
  let [path, query] = base.split('?')
  let queries = (query || '').split('&')
    .filter(s => s && s !== '')
    .map(itm => itm.split('='))
    .reduce((acc, itm) => { acc[itm[0]] = itm[1]; return acc }, {})
  // set or reset the value
  if (value) {
    queries[key] = encodedValue
  } else {
    delete queries[key]
  }
  // reconstruct url
  const combine = (s1, s2, delimiter) => s2 ? `${s1}${delimiter}${s2}` : s1
  const mapToPairs = m => Object.keys(m).map(k => `${k}=${m[k]}`)
  return combine(combine(path, mapToPairs(queries).join('&'), '?'), anchor, '#')
}
