<template>
    <div class="uc-single-select" v-if="getOptions && getOptions.length">
      <template v-if="compact">
        <div class="compact description" :title="description">{{description}}</div>
      </template>
      <template v-else>
        <label class="uc-input-label" :for="fieldId">{{definition.label}}</label>
        <div class="uc-input-wrapper" v-if="getOptions && getOptions.length < 6">
          <uc-radio class="uc-single-select__radio"
                    v-for="option in getOptions"
                    :readonly="definition.readonly"
                    :key="option.guid"
                    :label="option.label"
                    :guid="option.guid"
                    :checked="option.guid === selectedOption"
                    :group="fieldId"
                    @change="change" />
        </div>
        <template v-else>
          <uc-select :readonly="definition.readonly" :options="getOptions" :selected="selectedOption" :placeholder="definition.placeholder" @selected="change" />
        </template>
        <feedback v-for="error in definition.errors" :value="error" :key="error" type="error"></feedback>
      </template>
    </div>
</template>

<script>
import UcRadio from '../forms/uc-radio'
import UcSelect from '../forms/uc-select'
import GetId from '../../js/id-generator.js'
import feedback from '../widgets/field-feedback'
import createSaveCommand from '../../js/create-save-command'
export default {
  name: 'single-select',
  data () {
    return {
      fieldId: ''
    }
  },
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {
          options: []
        }
      }
    },
    compact: {
      type: Boolean,
      default: false
    },
    options: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    getOptions () {
      return this.options.length > 0 ? this.options : this.definition.options
    },
    selectedOption () {
      if (this.definition.selected && this.definition.selected.length > 0) {
        return this.definition.selected[0].guid ? this.definition.selected[0].guid : this.definition.selected[0]
      } else {
        return this.definition.value
      }
    },
    description () {
      if (!this.definition.options || !this.definition.options.length) return this.definition.value
      let option = this.definition.options.find(option => option.guid === this.selectedOption)
      if (!option) return this.definition.value
      return option.label
    }
  },
  components: {
    UcRadio,
    UcSelect,
    feedback
  },
  methods: {
    change (option) {
      const saveCommand = createSaveCommand(this.name, this.definition.guid, this.definition.label, [option.guid], this.definition.selected, 'selected')
      this.$emit('change', saveCommand)
    }
  },
  created () {
    this.fieldId = GetId()
  }
}
</script>

<style lang="stylus">
.uc-single-select
  .compact.description
    white-space nowrap
    overflow hidden
    text-overflow ellipsis
.uc-single-select__radio
  margin-bottom 7px
</style>
