<template>
  <div class="uc-short-text-editor uc-editor" :class="{compact}">
    <template v-if="compact">
        <label>{{ definition.value }} </label>
    </template>
    <template v-else>
        <uc-text-input
                :value="definition.value"
                :label="computedLabel"
                :isError="computedError"
                :error="definition.error"
                :errors="definition.errors"
                :placeholder="definition.placeholder"
                :readonly="definition.readonly"
                :tooltip="definition.description"
                :size="size"
                @focus="$emit('focus', $event)"
                @blur="onBlur"
                @input="onInput"/>
        <span class="required-field" v-show="!computedError && computedRequired">Required</span><feedback v-for="error in definition.errors" :value="error" :key="error" type="error"/>
    </template>
  </div>
</template>

<script>
import ucTextInput from '../forms/uc-text-input'
import feedback from '../widgets/field-feedback'
import createSaveCommand from '../../js/create-save-command'
export default {
  name: 'short-text',
  props: {
    name: {
      type: String,
      required: true
    },
    definition: {
      type: Object,
      default () {
        return {}
      }
    },
    compact: {
      type: Boolean,
      default: false
    },
    language: String,
    size: {
      type: String,
      default: () => 'small'
    }
  },
  computed: {
    computedLabel () {
      const lang = this.definition.multilingual && this.language !== undefined && this.language !== '' ? `(${this.language})` : ''
      return `${this.definition.label} ${lang}`
    },
    computedError () {
      if (this.definition.errors === undefined || this.definition.errors === null) {
        return false
      }

      return true
    },
    computedRequired () {
      return this.definition.hasOwnProperty('validation') ? this.definition.validation.required : false
    }
  },
  components: {
    ucTextInput,
    feedback
  },
  methods: {
    onBlur (e) {
      if (this.definition.value !== e.target.value || this.definition.error) {
        const saveCommand = createSaveCommand(this.name, this.definition.guid, this.definition.label, e.target.value, this.definition.value)
        this.$emit('change', saveCommand)
      }
    },
    onInput (value) {
      if (this.definition.value !== value || this.definition.error) {
        const saveCommand = createSaveCommand(this.name, this.definition.guid, this.definition.label, value, this.definition.value)
        this.$emit('onInput', saveCommand)
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.error
  color $col_red
  margin-left 16px

.uc-editor
  margin-top 15px
.compact
  margin-top 0

.required-field
  font-size 12px
  line-height 1.33
  float left
  margin 5px 10px 0px 16px
  color $ucommerce-secondary
</style>
