<template>
  <div class="field-feedback" :class="classes">{{ value }}</div>
</template>

<script>
import * as feedbackTypes from '../../js/feedback-types'
export default {
  name: 'field-feedback',
  props: {
    type: {
      type: String,
      default: feedbackTypes.info
    },
    value: String
  },
  computed: {
    classes () {
      return {
        info: this.type === feedbackTypes.info,
        warning: this.type === feedbackTypes.warning,
        error: this.type === feedbackTypes.error
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.field-feedback
  font-size 12px
  min-height 1.1rem
  &.info
    color $col_dark_gray
  &.warning
    color $col_black
  &.error
    color $col_red
</style>
