<template>
  <div class="catalogs">
    <div class="catalogs-inner">
      <section class="catalogs__builder-outer">
        <cat-builder ref="builder"></cat-builder>
      </section>
      <section class="catalogs__listing-outer">
        <div class="catalogs__listing-outer__right">
          <language-selector></language-selector>
        </div>
        <list :id="'Products'"
          :metadataUrl="'/products/definitions/full'"
          :dataUrl="dataUrl"
          :defaultColumnsConfiguration="{
            'primaryImage': { 'width': '90px' },
            'sku': {},
            'name': { 'width': '250px' },
            'definition': {},
            'eur 15 pct': { 'width': `${360 - 17}px` }
          }"
          @sortBy="sortBy"></list>
      </section>
      <undo :undos="undos" @undo="executeUndo"></undo>
      <form-feedback type="error" v-if="errorMessage">{{errorMessage}}</form-feedback>
    </div>
  </div>
</template>

<script>
import CatBuilder from '../../CatalogFoundation/components/catalog-builder/catalog-builder.vue'
import LanguageSelector from '../../CatalogFoundation/components/shared/language-selector'
import List from '../../CatalogFoundation/components/generic-list/list'
import Undo from '../../shared/components/widgets/undo-widget'
import FormFeedback from '../../shared/components/widgets/form-feedback'
import { nodeTypeApiMap } from '../js/tree-node-types'
import { createNamespacedHelpers, mapGetters, mapActions } from 'vuex'
const builderStore = createNamespacedHelpers('builder')

export default {
  name: 'products',
  components: {
    CatBuilder,
    LanguageSelector,
    List,
    Undo,
    FormFeedback
  },
  data () {
    return {
      dataUrl: null
    }
  },
  watch: {
    displayedNode () {
      this.setUrl()
    },
    contentLanguage () {
      this.setUrl()
    }
  },
  computed: {
    ...builderStore.mapGetters(['displayedNode']),
    ...mapGetters(['contentLanguage', 'undos', 'errorMessage', 'languages', 'contentLanguage'])
  },
  methods: {
    ...mapActions(['undo']),
    setUrl () {
      if (this.displayedNode === undefined || this.displayedNode.nodeType === undefined) return
      this.dataUrl = this.getUrl()
    },
    getUrl (columnKey) {
      return `/${nodeTypeApiMap[this.displayedNode.nodeType]}/${this.displayedNode.guid}/products/${this.contentLanguage}?sort=${columnKey}`
    },
    executeUndo (command) {
      this.undo(command)
    },
    sortBy (columnKey) {
      this.dataUrl = this.getUrl(columnKey)
    }
  }
}
</script>
<style lang="stylus" scoped>
.catalogs
  margin-bottom 30px
.catalogs__listing-outer__right
  display grid
  grid-column-gap 20px
  justify-items end
  margin-bottom 30px
</style>
