<template>
  <generic-details @resurrected="resurrected" @deleted="entityDeleted"></generic-details>
</template>

<script>
/*
*
* Listens to whatever is selected in the catalog builder, and
* loads the data from the right details API, and passes it on to
* the GenericDetails component for editing.
*
*/

import { nodeTypeApiMap } from '../../js/tree-node-types'
import GenericDetails from './generic-details.vue'
import { createNamespacedHelpers, mapGetters } from 'vuex'
const detailsStore = createNamespacedHelpers('details')
const builderStore = createNamespacedHelpers('builder')

export default {
  name: 'details-section',
  data () {
    return {
      loadDetailsLocked: false,
      bufferedDetailsGuid: null,
      currentDetailsGuid: null,
      typeNameMap: {
        productCategory: 'Category',
        productCatalog: 'Catalog'
      }
    }
  },
  computed: {
    ...builderStore.mapGetters(['displayedNode', 'dragInfo']),
    ...mapGetters(['contentLanguage'])
  },
  watch: {
    async displayedNode () {
      if (this.dragInfo.dragging) {
        return
      }
      if (!this.displayedNode.guid) {
        this.setDetails(undefined)
        return
      }
      this.bufferedDetailsGuid = this.displayedNode.guid
      await this.getDetails()
    }
  },
  components: {
    GenericDetails
  },
  methods: {
    ...detailsStore.mapMutations(['setDetails']),
    ...builderStore.mapMutations(['resetDisplayedNode']),
    getDetailsBase () {
      return this.axios.get(`${nodeTypeApiMap[this.displayedNode.nodeType]}/${this.displayedNode.guid}/${this.contentLanguage}`)
    },
    async getDetails () {
      if (!this.loadDetailsLocked) {
        this.loadDetailsLocked = true
        this.currentDetailsGuid = this.displayedNode.guid
        this.populateDetails(await this.getDetailsBase())
      }
    },
    populateDetails (details) {
      this.loadDetailsLocked = false
      if (this.currentDetailsGuid !== this.bufferedDetailsGuid) {
        this.getDetails()
        return
      }
      this.setDetails(details.data)
    },
    entityDeleted () {
      this.setDetails(null)
      this.resetDisplayedNode()
      this.$emit('deleted')
    },
    resurrected (nodeGuid) {
      this.$emit('resurrected', nodeGuid)
    }
  }
}
</script>

<style lang="stylus" scoped>
</style>
