<template>
  <div class="uc-datetime-picker" v-bind:class="getEditorClassObject">
    <template v-if="compact && definition.value">
      <label>{{ dateFormatter(definition.value) }}, {{ timeFormatter(timeData) }}</label>
    </template>
    <template v-if="!compact">
      <div class="input-fields-container">
        <div class="input-field-container">
            <label class="input-label" v-if="definition.label" :class="{ error: definition.error }">{{definition.label}}</label>

            <div class="input-field-wrapper">
                <date-picker class="date-picker"
                  :disabled="definition.readonly"
                  :value="definition.value"
                  :format="dateFormatter"
                  :placeholder="definition.label"
                  :full-month-name="true"
                  :calendar-class="'calendar'"
                  :wrapper-class="'wrapper'"
                  @selected="changeDate"/>
            </div>

        </div>
        <div class="input-field-container input-spacer">
            <label class="input-label" v-if="definition.label" :class="{ error: definition.error }">{{definition.label}}</label>
            <div class="input-field-wrapper">
                <time-picker class="time-picker"
                  :disabled="definition.readonly"
                  :hide-clear-button="true"
                  :value="timeData"
                  format="HH:mm"
                  @change="changeTime"/>
            </div>
        </div>
      </div>
      <feedback v-for="error in definition.errors" :value="error" :key="error" type="error"/>
    </template>
  </div>
</template>

<script>
import DatePicker from 'vuejs-datepicker'
import TimePicker from 'vue2-timepicker'
import { Format, locales } from '../../js/date-formatter'
import CreateSaveCommand from '../../js/create-save-command'
import feedback from '../widgets/field-feedback'
export default {
  name: 'uc-date-time-picker',
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {}
      }
    },
    compact: {
      type: Boolean,
      default: false
    },
    format: {
      type: String,
      default: 'DD. MMMM YYYY'
    },
    locale: {
      type: String,
      default: 'en'
    },
    label: String
  },
  computed: {
    timeData () {
      const date = this.getDate()
      if (date) {
        return {
          HH: date.getHours().toString().padStart(2, '0'),
          mm: date.getMinutes().toString().padStart(2, '0')
        }
      } else {
        return { HH: undefined, mm: undefined }
      }
    },
    getEditorClassObject () {
      return {
        'uc-editor': !this.compact
      }
    }
  },
  components: {
    DatePicker,
    TimePicker,
    feedback
  },
  methods: {
    getDate () {
      if (this.definition.value) {
        const date = new Date(this.definition.value)
        if (date instanceof Date && !isNaN(date)) {
        // Truncate date to make comparison possible
          return new Date(date.getFullYear(), date.getMonth(), date.getDate(), date.getHours(), date.getMinutes())
        } else {
          return undefined
        }
      } else {
        return undefined
      }
    },
    changeTime (time) {
      const date = this.getDate() || new Date()
      if (date) {
        const dateTime = new Date(
          date.getFullYear(), date.getMonth(), date.getDate(), time.data.HH, time.data.mm)
        this.emitIfChanged(dateTime)
      }
    },
    changeDate (newDate) {
      const date = this.getDate() || new Date()
      if (date) {
        const dateTime = new Date(
          newDate.getFullYear(), newDate.getMonth(), newDate.getDate(), date.getHours(), date.getMinutes())
        this.emitIfChanged(dateTime)
      }
    },
    emitIfChanged (dateTime) {
      const previousDate = this.getDate() || new Date()
      if (dateTime.getTime() !== previousDate.getTime()) {
        const command = CreateSaveCommand(this.name, this.definition.guid, this.definition.label, dateTime, previousDate)
        this.$emit('change', command)
      }
    },
    dateFormatter (date) {
      return Format(date, this.format, locales[this.locale])
    },
    timeFormatter () {
      return `${this.timeData.HH}:${this.timeData.mm}`
    }
  }
}
</script>

<style lang="stylus" scoped>

.input-fields-container:after
  content ""
  display table
  clear both

.input-spacer
  margin-left 20px

.input-field-container
  width calc(50% - 10px)
  background-color #fff
  float left
  position relative
  :first
    margin-right 5px
  .input-field-wrapper
    line-height 50px
    background-color #fff
    border 1px #e1e1e8 solid
    border-radius 4px

.input-label-container
  .input-label-wrapper
    position relative
    overflow hidden

.input-label
  display inline-block
  color $ucommerce-secondary
  line-height 12px
  background-color #ffffff
  transition-duration: 200ms
  font-size 12px
  font-weight normal
  top -7px
  left 14px
  padding 0 4px
  position absolute
  z-index 15

  &.time-pick
    right 0
    left 50%

.uc-datetime-picker
  position relative

.uc-datetime-picker__group
  display flex
  position relative

.time-picker
  input[type=text].display-time
    height 32px
    text-align right
    font-size 16px
    padding 0
    font-size 16px
    width 100%

    &.disabled
      color $col_dark
  .clear-btn
    display none
.error
  color $col_red

.uc-datetime-picker__tooltip
  margin-left 5px

/*  Deep styling the 3rd party libraries datepicker and datetimepicker*/
.date-picker
  &.uc-editor
    position relative
    margin-top 10px
  width 100%
  position relative
  background-color transparent
  & >>> .calendar
    top 52px
    z-index 2000
    border-radius 4px
  & >>> .cell:not(.blank):not(.disabled).day:hover
  & >>> .cell:not(.blank):not(.disabled).month:hover
  & >>> .cell:not(.blank):not(.disabled).year:hover
    border 1px solid $ucommerce-light-blue
  & >>> .cell.day.selected
  & >>> .cell.month.selected
  & >>> .cell.year.selected
    background $ucommerce-light-blue
  & >>> input[type=text]
    padding 0 16px
    width 100%
    cursor pointer

.time-picker
  width 100%
  font-family $base_font_fam
  font-size 16px
  vertical-align inherit
  & >>> ul > li
    line-height 25px
  & >>> ul.hours > li:not(.hint):not(.disabled):not(.active)
  & >>> ul.minutes > li:not(.hint):not(.disabled):not(.active)
    border 0px solid transparent
  & >>> ul.hours > li:not(.hint):not(.disabled):not(.active):hover
  & >>> ul.minutes > li:not(.hint):not(.disabled):not(.active):hover
    color #ffffff
    background $ucommerce-light-blue
  & >>> ul.hours > li:not(.hint):not(.disabled).active
  & >>> ul.minutes > li:not(.hint):not(.disabled).active
    background $ucommerce-light-blue
  & >>> input[type=text]
    border 0px solid $builder-selected-node-bg
    text-align left
    width 100%
    padding 0 16px
    font-size 16px
    height 32px
    overflow hidden
    cursor pointer
  & >>>.dropdown
      top 53px
      width 100%
      border-radius 4px
      > .select-list
          width 100%
        .hint
          display none

</style>
