<template>
  <div class="uc-single-select">
    <template v-if="compact">
      <label>{{description}}</label>
    </template>
    <template v-else>
      <div v-if="availableDefinitions && availableDefinitions.length">
        <uc-single-select
                :definition="definition"
                :compact="compact"
                :name="'definition'"
                :options="availableDefinitions"
                @change="onChange">
        </uc-single-select>
      </div>
    </template>
  </div>
</template>

<script>
import GetId from '../../js/id-generator.js'
import createSaveCommand from '../../js/create-save-command'
import { nodeTypeApiMap } from '../../../CatalogFoundation/js/tree-node-types'
import { createNamespacedHelpers } from 'vuex'
import UcSingleSelect from '../../../shared/components/editors/uc-single-select'

const detailsStore = createNamespacedHelpers('details')

export default {
  name: 'definition-picker',
  data () {
    return {
      fieldId: '',
      availableDefinitions: []
    }
  },
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {
          options: []
        }
      }
    },
    compact: {
      type: Boolean,
      default: false
    }
  },
  async mounted () {
    if (!this.compact) this.availableDefinitions = await this.loadDefinitions()
  },
  computed: {
    ...detailsStore.mapGetters(['details', 'validationErrors']),
    description () {
      return this.definition.value || (
        this.definition.options && this.definition.selected.length
          ? this.definition.options[this.definition.selected[0]].label
          : ''
      )
    }
  },
  components: {
    UcSingleSelect
  },
  methods: {
    change (item) {
      const saveCommand = createSaveCommand(this.name, this.definition.guid, this.definition.label, [item.guid], this.definition.selected, 'selected')
      this.$emit('change', saveCommand)
    },
    async loadDefinitions () {
      const definitions = await this.axios.get(`${nodeTypeApiMap[this.details.meta.nodeType]}/definitions`)
      return definitions.data.map(definition => { return { guid: definition.guid, label: definition.name } })
    },
    onChange (saveCommand) {
      this.$emit('onInput', saveCommand)
    }
  },
  created () {
    this.fieldId = GetId()
  }
}
</script>

<style lang="stylus" scoped>
</style>
