<template>
  <div class="uc-rich-text uc-editor" :class="{readonly, focused, blurred, compact}">
    <template v-if="compact">
      <div class="compact description" :title="definition.value" v-html="definition.value"></div>
    </template>
    <template v-else>
      <label @click="setFocusAndFocus" v-if="definition.label">{{ computedLabel }}</label>
      <div class="input-field-container">
        <div class="input-field-wrapper">
          <div class="feedback-icon-wrap">
            <div class="feedback-icon">
              <uc-icon v-show="!this.computedError && !definition.readonly && condition === 'Active'" icon="ArrowBack" />
              <uc-icon v-show="this.computedError" icon="Asterix" />
            </div>
          </div>
          <div ref="editor" @click="setFocusAndFocus" />
        </div>
      </div>
      <span class="required-field" v-show="!computedError && computedRequired">Required</span><feedback v-for="error in definition.errors" :value="error" :key="error" type="error"></feedback>
    </template>
  </div>
</template>

<script>
import Quill from 'quill'
import createSaveCommand from '../../js/create-save-command'
import debounce from 'debounce'
import feedback from '../widgets/field-feedback'
import ucIcon from '../../../shared/icons/uc-icon'

export default {
  name: 'uc-rich-text',
  data () {
    return {
      editor: null,
      focused: false,
      blurred: true,
      inputValue: '',
      condition: 'Idle'
    }
  },
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {}
      }
    },
    language: String,
    theme: {
      type: String,
      default: 'snow'
    },
    readonly: Boolean,
    placeholder: {
      type: String,
      default: ''
    },
    label: String,
    compact: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    'definition.value': function () {
      if (this.compact) return
      if (this.definition.value === undefined || this.definition.value === this.editor.root.innerHTML) {
        return
      }
      this.editor.root.innerHTML = this.definition.value
      this.$nextTick(function () {
        this.editor.on('text-change', this.update)
      })
    }
  },
  mounted () {
    if (this.compact) return

    // Setting up Quill Editor.
    this.editor = new Quill(this.$refs.editor, {
      modules: {
        toolbar: [
          [{ header: [1, 2, false] }],
          ['bold', 'italic', 'underline'],
          ['image', 'code-block']
        ]
      },
      theme: this.theme,
      readOnly: this.definition.readonly,
      placeholder: this.placeholder
    })

    // Checking for definition value.
    if (this.definition.value !== null) {
      this.editor.root.innerHTML = this.definition.value
      this.inputValue = this.definition.value
    } else {
      this.inputValue = ''
    }

    // Listeners on the editor.
    this.editor.on('text-change', this.update)
    this.editor.on('selection-change', this.setSelectionChange)

    this.focused = !this.isEmptyValidator()
  },
  created () {
    // Setting Definition Value to Input Value.
    this.inputValue = this.definition.value
  },
  computed: {
    computedLabel () {
      const lang = this.definition.multilingual ? `(${this.language})` : ''
      return `${this.definition.label} ${lang}`
    },
    computedError () {
      return !(this.definition.errors === undefined || this.definition.errors === null)
    },
    computedRequired () {
      return this.definition.hasOwnProperty('validation') ? this.definition.validation.required : false
    }
  },
  components: {
    feedback,
    ucIcon
  },
  methods: {
    update: debounce(function () {
      const value = this.editor.root.innerHTML
      this.inputValue = this.editor.root.innerHTML

      if (value === this.definition.value) {
        return
      }
      const saveCommand = createSaveCommand(this.name, this.definition.guid, this.definition.label, value, this.definition.value)
      this.$emit('change', saveCommand)

      if (this.focused && this.condition !== 'Active') {
        this.condition = 'Active'
      }
    }, 500),
    setSelectionChange: debounce(function (event) {
      if (event === null) {
        this.focused = !this.isEmptyValidator()
        if (this.condition !== 'Idle') {
          this.condition = 'Idle'
          this.blurred = true
        }
      } else {
        this.blurred = false
      }
    }, 500),
    setFocusAndFocus: function () {
      this.focused = true
    },
    isEmptyValidator () {
      return (this.inputValue === '<p><br></p>' || this.inputValue === '<pre class="ql-syntax" spellcheck="false">\n' +
              '</pre>' || this.inputValue === '')
    }
  }
}
</script>

<style lang="stylus">
  @import "../../stylus/_vendor/quill/snow.styl"
  @import "../../stylus/_vendor/quill/base.styl"
  @import "../../stylus/_vendor/quill/core.styl"
  .ql-toolbar.ql-snow
    border-bottom solid #e1e1e8 1px
</style>
<style lang="stylus" scoped>
.uc-editor
  margin-top 15px
  font-family $base_font_fam
.compact
  margin-top 0

.input-field-container
  border-radius 4px
  background-color #fff

  .input-field-wrapper
    padding 12px
    border-radius 4px
    background-color #fff
    border 1px #e1e1e8 solid

  &.disabled
    background-color #f7f7f9

  &.danger
    background-color $col_red

.uc-rich-text
  position relative

  .ql-container.ql-snow
    padding-right 30px
    padding-left 2px
    font-size 16px
    color $ucommerce-editors-active-input-color

  .feedback-icon-wrap
    position relative

    .feedback-icon
      position absolute
      z-index 200
      right 4px
      margin-top 47px

  &.focused
    label
      transform: translateY(-81px)
      background-color: #fff
      line-height: 20px
      font-size: 12px
      padding: 0 4px

  &.readonly
    label
      background-color: #f7f7f9
    .input-field-container
      background-color #f7f7f9
      .input-field-wrapper
        background-color #f7f7f9
        border 1px #e1e1e8 solid
    .ql-container.ql-snow
      color #cacad7

  &.blurred
    .ql-container.ql-snows
      color $ucommerce-editors-idle-input-color

  label
    position absolute
    color $ucommerce-secondary
    line-height 30px
    left 14px
    top 71px
    z-index 9
    font-size 16px
    padding 0 2px 0 2px
    background-color #fff
    transition-duration: 200ms

    .error
      color $col_red
    .ql-editor
      display block
      padding 12px 6px
      &.ql-blank::before
        left 6px
    .uc-rich-text .input-label
      display inline-block
    .uc-rich-text__tooltip
      margin-left 5px
</style>
