import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import Axios from 'axios'
import VueI18n from 'vue-i18n'
import messages from './translations/_main'
import truncateTooltip from '../shared/js/trucate-tooltip'
import { Format, locales } from '../shared/js/date-formatter'

Vue.config.productionTip = false

// Setup Axios as http handler for Vue (including license interceptor)
const axiosInstance = Axios.create({
  baseURL: '/ucommerceapi/v2/'
})
Vue.prototype.axios = axiosInstance

// Language versioning lib setup

Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: 'en',
  fallbackLocale: 'en',
  messages
})

Vue.filter('format', function (value, format, locale) {
  if (!value) return ''
  value = value.toString()
  return Format(value, format, locales[locale])
})

Vue.directive('truncate', truncateTooltip)

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
