<template>
  <div class='list-table-container' ref="body">
    <section class="draggable-container">
      <draggable v-model="shownColumns" handle='.handle' animation='150' class='draggable' ref="draggable">
        <div v-for="column in shownColumns" :key="column.index" class='column' :style="defaultColumnStyle" ref="columnRefs"
          :data-key="column.key"
          @dragstart="startDrag"
          @dragend="toggleDragged">
          <div class='sticky cell column_header handle'
            :class="{sorted : isSortedByDescending(column.key) || isSortedByAscending(column.key)}"
            @click="sortBy(column.key)"
            @mouseenter="hoveredColumn = column"
            @mouseleave="hoveredColumn = null">
            <div v-truncate class="header-label">{{ column.label }}</div>
            <sortable-sorted-descending class="sortable" v-if="isSortedByDescending(column.key)"></sortable-sorted-descending>
            <sortable-sorted-ascending class="sortable" v-if="isSortedByAscending(column.key)"></sortable-sorted-ascending>
            <resize-handler class="resize-handler" v-show="!resizeObj.headerRef && hoveredColumn && hoveredColumn.key === column.key"
              :size="resizeObj.resizeWidth"
              :minSize="resizeObj.minWidth"
              :isVertical="false"
              @resizeMousedown="onMousedown(column.key)"
              @emitDelta="onMousemoveDebounced($event, 250)"
              @resizeMouseup="onMouseup(column.key, 'width')"></resize-handler>
          </div>
          <div class="column-rows" v-show="list.rows.length > 0" >
            <div v-for="row in list.rows" :key="row.meta.entityId" class='cell' @click="$emit('rowClick', row)"
                  :class="{hover: hoveredRow === row}"
                  @mouseenter="hoveredRow = row"
                  @mouseleave="hoveredRow = null">
              <template v-if="row[column.key]">
                <component :is="column.editor" :name="column.label" :compact="true"
                            :definition="{...row[column.key], readonly: readonly}"></component>
              </template>
            </div>
            <div class="remaining items placeholder cell" :style="remainingItemsStyle" ref="remaining"></div>
          </div>
        </div>
        <div :key="'spacerColumnRight'" :style="fillerStyle" ref="filler">
          <div class='sticky cell filler column_header'>
            <slot></slot>
          </div>
          <div class="column-rows">
            <div v-for="row in list.rows" :key="row.meta.entityGuid" class='cell filler' :class="{hover: hoveredRow === row}"></div>
            <div class="remaining items placeholder cell filler" :style="remainingItemsStyle"></div>
          </div>
        </div>
      </draggable>
    </section>
    <section class="resize-box" :style="resizeBoxStyle" v-show="resizeObj.headerRef">
      <div class="resize-indicator" :style="resizeIndicatorStyle"></div>
    </section>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
import { isScrolledIntoView } from '../../../shared/js/scroll'
import debounce from 'debounce'
import ResizeHandler from '../shared/resize-handler'
import Editors from '../../../shared/components/editors'
import SortableUnsorted from '../../../shared/icons/sortable-unsorted'
import SortableSortedAscending from '../../../shared/icons/sortable-sorted-ascending'
import SortableSortedDescending from '../../../shared/icons/sortable-sorted-descending'
import { createNamespacedHelpers } from 'vuex'
const genericListStore = createNamespacedHelpers('genericList')

export default {
  name: 'ListTable',
  components: {
    draggable,
    ResizeHandler,
    ...Editors,
    SortableUnsorted,
    SortableSortedAscending,
    SortableSortedDescending
  },
  data () {
    return {
      headerScrollTop: 0,
      sortedBy: '',
      hoveredRow: undefined,
      hoveredColumn: undefined,
      resizeObj: {
        minWidth: 100,
        defaultWidth: 150,
        widthsApplied: false,
        headerRef: null,
        resizeWidth: 0,
        resizeOffset: 0,
        fillerWidth: 0,
        containerDefaultWidth: 0
      }
    }
  },
  props: {
    list: {
      type: Object,
      required: true
    },
    readonly: {
      type: Boolean,
      default: true
    },
    totalCount: {
      type: Number,
      required: true
    }
  },
  mounted () {
    window.addEventListener('scroll', debounce(this.checkIfMoreItemsNeeded, 250))
  },
  computed: {
    shownColumns: {
      get () {
        return this.list.columns.filter(x => x.show)
      },
      set (shownColumns) {
        let hiddenColumns = this.list.columns.filter(x => !x.show)
        let resultColumns = [ ...shownColumns, ...hiddenColumns ]
        this.$emit('columnsDragged', resultColumns)
        this.$forceUpdate()
      }
    },
    remainingItemsStyle () {
      const rowHeight = 70
      const placeholderHeight = rowHeight * (this.totalCount - this.list.rows.length)
      return { height: `${placeholderHeight}px`, border: 'none' }
    },
    defaultColumnStyle () {
      return { width: `${this.resizeObj.defaultWidth}px` }
    },
    resizeBoxStyle () {
      if (!this.resizeObj.headerRef) return {}
      const height = Math.floor(this.resizeObj.headerRef.getBoundingClientRect().height)
      return {
        height: `${height}px`,
        width: `${this.resizeObj.resizeWidth}px`,
        left: `${this.resizeObj.resizeOffset}px`
      }
    },
    resizeIndicatorStyle () {
      if (!this.resizeObj.headerRef) return {}
      const height = this.$refs.draggable.$el.getBoundingClientRect().height
      return { height: `${height}px` }
    },
    fillerStyle () {
      return { width: `${this.resizeObj.fillerWidth}px` }
    }
  },
  methods: {
    ...genericListStore.mapActions(['saveColumnClientConfiguration', 'getColumnsClientConfiguration']),
    sortBy (columnKey) {
      if (columnKey === this.sortedBy) {
        let invertedColumnKey = this.toColumnKeyDescending(columnKey)
        this.$emit('sortBy', invertedColumnKey)
        this.sortedBy = invertedColumnKey
      } else {
        if (this.sortedBy === this.toColumnKeyDescending(columnKey)) {
          this.$emit('sortBy', '')
          this.sortedBy = ''
        } else {
          this.$emit('sortBy', columnKey)
          this.sortedBy = columnKey
        }
      }
    },
    toColumnKeyDescending (columnKey) {
      return `-${columnKey}`
    },
    isSortedByDescending (columnKey) {
      return this.toColumnKeyDescending(columnKey) === this.sortedBy
    },
    isSortedByAscending (columnKey) {
      return columnKey === this.sortedBy
    },
    isNotSortedBy (columnKey) {
      return !this.isSortedByAscending(columnKey) && !this.isSortedByDescending(columnKey)
    },
    startDrag (e) {
      e.dataTransfer.setDragImage(new Image(), 0, 0)
      this.toggleDragged(e)
    },
    toggleDragged (e) {
      let rows = e.target.childNodes
      rows.forEach(x => x.classList.toggle('dragged_column_row'))
      rows[0].classList.remove('dragged_column_row')
      rows[0].classList.toggle('dragged_column_header')
    },
    async checkIfMoreItemsNeeded () {
      const remainingItemsPlaceholder = this.$refs.remaining
      if (remainingItemsPlaceholder.length && isScrolledIntoView(remainingItemsPlaceholder[0])) {
        this.$emit('needsMoreData', this.section)
      }
    },

    async initColumnsConfiguration (defaultConfiguration, fieldName) {
      if (this.resizeObj.widthsApplied) return
      this.resizeObj.widthsApplied = true
      this.resizeObj.containerDefaultWidth = this.calcContainerWidth()
      const clientConfiguration = await this.getColumnsClientConfiguration({ currentList: this.list })
      this.applyConfiguration(defaultConfiguration, fieldName)
      this.applyConfiguration(clientConfiguration, fieldName)
      this.resizeFiller()
    },

    applyConfiguration (configuration, fieldName) {
      const currentList = this.list
      Object.keys(configuration).forEach(columnKey => {
        let header = this.$refs.columnRefs.find(header => header.dataset.key === columnKey)
        if (!header) return

        let fieldValue = configuration[columnKey][fieldName]
        header.style.width = fieldValue
        if (fieldValue) {
          fieldValue = `${Math.floor(header.getBoundingClientRect().width)}px`
          this.saveColumnClientConfiguration({ currentList, columnKey, fieldName, fieldValue })
        }
      })
    },

    // --- Column resizing helpers
    resizeFiller () {
      const containerDefaultWidth = this.resizeObj.containerDefaultWidth
      const containerWidth = this.calcContainerWidth()
      const headersWidth = this.calcHeadersWidth()
      let result = 0

      // Filler column taking up remainder of container
      let fillerWidth = containerWidth - headersWidth
      if (fillerWidth > 0) {
        result = fillerWidth
      }
      // Filler column shrinks when resized header approaches container end
      if (headersWidth + fillerWidth > containerDefaultWidth) {
        result = containerDefaultWidth - headersWidth
      }
      // No filler column when resized header ends beyond container
      if (headersWidth > containerDefaultWidth) {
        result = 0
      }
      this.resizeObj.fillerWidth = result
    },

    calcContainerWidth () {
      return Math.floor(this.$refs.draggable.$el.getBoundingClientRect().width)
    },
    calcHeadersWidth () {
      if (this.$refs.columnRefs.length === 0) return 0
      return this.$refs.columnRefs
        .map(header => header.getBoundingClientRect().width)
        .reduce((sum, width) => sum + Math.floor(width))
    },
    calcHeaderWidth () {
      if (!this.resizeObj.headerRef) return 0
      return Math.floor(this.resizeObj.headerRef.getBoundingClientRect().width)
    },
    calcResizeOffset () {
      if (!this.resizeObj.headerRef) return 0
      const offset = this.resizeObj.headerRef.getBoundingClientRect().left - this.$refs.body.getBoundingClientRect().left
      return Math.floor(offset)
    },

    // --- Column resizing handlers
    onMousemoveDebounced (event, delay) {
      debounce(this.onMousemove(event), delay)
    },
    onMousedown (columnKey) {
      this.resizeObj.headerRef = this.$refs.columnRefs.find(header => header.dataset.key === columnKey)
      this.resizeObj.resizeWidth = this.calcHeaderWidth()
      this.resizeObj.resizeOffset = this.calcResizeOffset()
    },
    onMousemove (newWidth) {
      if (!this.resizeObj.headerRef) return
      this.resizeObj.resizeWidth += newWidth
    },
    onMouseup (columnKey, fieldName) {
      if (!this.resizeObj.headerRef) return
      const header = this.resizeObj.headerRef
      const currentList = this.list
      const fieldValue = header.style.width = `${this.resizeObj.resizeWidth}px`
      this.saveColumnClientConfiguration({ currentList, columnKey, fieldName, fieldValue })
      this.resizeFiller()
      this.resizeObj.headerRef = null
    }
  },
  watch: {
    list () {
      this.$nextTick(() => this.checkIfMoreItemsNeeded())
    },
    shownColumns () {
      if (!this.resizeObj.widthsApplied) return
      this.$nextTick(() => this.resizeFiller())
    }
  }
}
</script>

<style lang="stylus" scoped>
  @keyframes fadein
    from { opacity: 0.3 }
    to   { opacity: 1 }
.list-table-container
  background-color #fff
  position relative
  overflow hidden

  .draggable-container
    position relative
    height calc(100vh - 216px)
    overflow scroll
    padding-bottom 20px
  .draggable
    position absolute
    min-width 100%
    display flex
  .sticky
    position sticky
    top 0
    z-index 100

  .column
    position relative
  .column_header.sorted
    text-decoration underline
    text-decoration-color $secondary-text
  .cell.column_header
    cursor pointer
    border none
    height 50px
    background $platform-bg
    padding-right 10px

  $cell_height = 70px
  .cell
    display flex
    align-items center
    padding 0 20px
    background-color #fff
    height $cell_height
    border-bottom 1px solid $col_border
    animation fadein 1s
    cursor pointer
    primary-body-text()
    > div:not(.sort-icon-container):not(.settings-container):not(.header-label)
      truncate()
    &.filler
      padding 0
      cursor default
    &.hover
      background lighten($ucommerce-blue, 95%)

  .dragged_column_header
    background-color $col_default_dragged_column_header
  .dragged_column_row
    background-color $col_default_dragged_column_row
  .remaining.items.placeholder
    background url(../../../shared/assets/placeholder.gif) repeat-y
    margin-left -8px

  /deep/ .input-label // To be removed when all editors have compact mode
    display none

.resize-box
  position absolute
  top 0
  z-index 101
.resize-handler
  position absolute
  right 4px
.resize-indicator
  position absolute
  right 0
  width 2px
  background $secondary-text

.header-label
  position relative
  secondary-details-text()
  line-height $cell_height
.sortable
  box(12px, 20px)
  margin-left 6px

.remaining.items.placeholder.cell
  border none
</style>
