export default function (details, save) {
  for (let propertyName in save) {
    const property = details[propertyName]
    if (!property) {
      continue
    }
    const { value, selected } = save[propertyName]
    const { validation } = property
    if (validation) {
      let messages = []
      if (validation.required && !(value !== undefined || selected)) {
        messages.push((validation.errorMessage || `'${property.label}' must not be empty.`))
      }
      if (validation.regex && !((new RegExp(validation.regex)).test(value))) {
        messages.push((validation.errorMessage || `'${property.label}' is not in a valid format.`))
      }
      if (messages.length) {
        return {
          [propertyName]: messages
        }
      }
    }
  }
  return null
}
