export default (definitionName, definitionGuid, label, newValue, oldValue, valueFieldName = 'value') => {
  const undo = {
    undo: {
      [definitionName]: {
        guid: definitionGuid,
        [valueFieldName]: oldValue
      }
    }
  }
  const meta = {
    meta: {
      label,
      contentLanguage: undefined,
      timeStamp: new Date(),
      nodeType: '',
      nodeGuid: '',
      undoMessage: undefined,
      undoAction: undefined
    }
  }
  const save = {
    save: {
      [definitionName]: {
        guid: definitionGuid,
        [valueFieldName]: newValue
      }
    }
  }
  if (oldValue !== undefined) {
    return { key: definitionGuid, ...save, ...undo, ...meta }
  } else {
    return { key: definitionGuid, ...save, ...meta }
  }
}
