<template>
  <section v-if="details && details.meta" class="details-view">
    <div class="details-header-placeholder">
      <header ref="headerContainer" class="details-header">
          <div class="header-top">
            <div class="name-group">
              <h1 name="name" v-truncate class="details-title">{{details.name.value}}</h1>
              <v-popover offset="18">
                <btn-round size="24px">
                  <icon-more></icon-more>
                </btn-round>
                <template slot="popover">
                  <div class="actions-container">
                    <div class="action" title="Rename" @click="openDialog(details.name.guid)" v-close-popover>{{$t('details.rename', {label: ''})}}</div>
                    <div class="action" title="Change definition" @click="openDialog('definition')" v-close-popover>{{$t('details.changeDefinition')}}</div>
                    <div class="action" title="Change SKU" v-if="details.sku" @click="openDialog(details.sku.guid)" v-close-popover>{{$t('details.changeSKU')}}</div>
                    <div class="action delete" :title="deleteLabel" @click="deleteEntity" v-close-popover>
                       {{ deleteLabel }}
                    </div>
                  </div>
                  <hr class="separator-line">
                  <div class="audit-container">
                    <div class="audit-item">
                      <span class="audit-header">{{$t('details.created')}}</span>
                      <br/>
                      <span class="audit-text">{{details.meta.createdOn | format('DD. MMM YYYY', uiLanguage) }} {{$t('details.by')}} {{details.meta.createdBy}}</span>
                    </div>
                    <div class="audit-item">
                      <span class="audit-header">{{$t('details.modified')}}</span>
                      <br/>
                      <span class="audit-text">{{details.meta.modifiedOn | format('DD. MMM YYYY', uiLanguage) }} {{$t('details.by')}} {{details.meta.modifiedBy}}</span>
                    </div>
                  </div>
                </template>
              </v-popover>
            </div>
            <language-selector></language-selector>
          </div>
          <div class="header-bottom">
            <p v-show="!isHeaderFixed" v-truncate class="details-subheader"> Definition: {{definitionName}}</p>
            <p v-show="!isHeaderFixed" v-truncate class="details-subheader" v-if="details.sku">{{details.sku.label}}: {{details.sku.value}}</p>
          </div>
      </header>
    </div>
    <div class="details-content">
      <div class="field-section-navigation" v-if="details.media || details.primaryImage">
        <ul class="section-nav-ul">
          <li class="section-nav"
              :class="{ active: isActiveNavField(nodeType) }"
              @click="scrollToElement(nodeType, true)">
            {{ $tc('details.basicInformation') }}
          </li>
          <li v-truncate class="section-nav"
              :class="{ active: isActiveNavField(mediaType) }"
              @click="scrollToElement(mediaType, true)" >
            {{ $tc('details.media') }}
          </li>
          <li v-truncate class="section-nav"
              v-for="property in dynamicProperties"
              :class="{ active: isActiveNavField(details[property].guid) }"
              :key="details[property].guid"
              @click="scrollToElement(details[property].guid, true), setActiveNavField(details[property].guid)">
            {{details[property].label}}
          </li>
        </ul>
      </div>
      <div class="field-section-fields">
        <fields-section :ref="nodeType" :data-guid="nodeType" @sectionClicked="setActiveNavField(nodeType)">
          <component :is="nodeType" :language="contentLanguage" @change="saveChange"></component>
        </fields-section>
        <!--Products-->
        <fields-section v-if="details.primaryImage" :heading="$tc('details.media')" :data-guid="details.primaryImage.guid" :ref="details.primaryImage.guid">
          <div class="media-section">
            <component :is="details.primaryImage.editor" :with-label="true" :definition="details.primaryImage" name="primaryImage" :multi-select="false" content-type="media"
                       @change="saveChange" ></component>
            <component :is="details.thumbnailImage.editor" :with-label="true" :definition="details.thumbnailImage" name="thumbnailImage" :multi-select="false" content-type="media"
                       @change="saveChange"></component>
          </div>
        </fields-section>
        <!--Catalogs-->
        <fields-section v-if="details.media" :heading="$tc('details.media')" :data-guid="details.media.guid" :ref="details.media.guid">
          <div class="media-section align-left">
            <component :is="'media'" :with-label="true" :definition="details.media" :name="'media'" :multi-select="false" content-type="media"
                       @change="saveChange" ></component>
          </div>
        </fields-section>
        <template v-if="dynamicProperties.length > 0">
          <fields-section :isDynamicProperties="true" v-for="property in dynamicProperties"
                          :key="details[property].guid" :heading="details[property].label"
                          :ref="details[property].guid" :data-guid="details[property].guid"
                          @sectionClicked="setActiveNavField(details[property].guid)"
          >
            <component
                    :is="details[property].editor"
                    :language="contentLanguage"
                    :definition="details[property]"
                    @change="saveChange"
                    :name="property">
            </component>
          </fields-section>
        </template>
      </div>
    </div>
    <form-feedback type="warning" v-if="anyValidationErrors" class="validation feedback">
      <h5>Please take another look at the content of these fields:</h5>
      <ul>
        <!-- eslint-disable vue/valid-next-line vue/no-unused-vars -->
        <li v-for="(messages, name, index) in validationErrors" :key="`anchor_${name}`">
          <a :href="`#${name}`">{{details[name].label}}</a>
          <ul v-for="message in messages" :key="message">
            <li>{{message}}</li>
          </ul>
        </li>
      </ul>
    </form-feedback>
    <property-editor-dialog
            v-if="dialogOpen === details.name.guid"
            :name="'name'"
            :definition="details.name"
            :custom-header="nameEditorCustomHeader"
            @hideDialog="closeDialogs"
            @onInput="onInput"></property-editor-dialog>
    <property-editor-dialog
            v-if="details.sku && dialogOpen === details.sku.guid"
            :name="'sku'"
            :definition="details.sku"
            @hideDialog="closeDialogs"
            @onInput="onInput"></property-editor-dialog>
    <property-editor-dialog
            v-if="dialogOpen === 'definition'"
            :name="'definition'"
            :definition="details.definition"
            @hideDialog="closeDialogs"
            @onInput="onInput"></property-editor-dialog>
  </section>
</template>

<script>
/*
  *
  *  Displays details of and allows editing catalogs, categories, products, variants. Anything that complies with the shared data format
  *  It's main data source is the details store, and all editors rendered by generic-details will be bound to the properties of
  *  `this.details`. However, this control is agnostic of what specific-details component is rendered. It may be CatalogDetails, ProductDetails
  *  or some customer provided component.
  *
  */

import { nodeTypeDefinitionGuidMap, nodeTypeSingular } from '../../js/tree-node-types'
import InfoWidget from './info-widget.vue'
import { mapGetters, mapMutations, mapActions, createNamespacedHelpers } from 'vuex'
import UcSelectSimple from '../../../shared/components/forms/uc-select-simple'
import createSaveCommand from '../../../shared/js/create-save-command'
import FormFeedback from '../../../shared/components/widgets/form-feedback'
import Editors from '../../../shared/components/editors'
import SpecificDetails from './specific-details'
import ShortText from '../../../shared/components/editors/uc-short-text'
import FieldsSection from './fields-section'
import LanguageSelector from '../../../CatalogFoundation/components/shared/language-selector'
import BtnRound from '../../../shared/components/widgets/btn-round'
import IconMore from '../../../shared/icons/more'
import UcText from '../../../shared/components/forms/uc-text-input'
import PropertyEditorDialog from '../../../CatalogFoundation/components/shared/property-editor-dialog'
import Simplebar from 'simplebar-vue'
import 'simplebar/dist/simplebar.min.css'

const detailsStore = createNamespacedHelpers('details')
const builderStore = createNamespacedHelpers('builder')

export default {
  name: 'generic-details',
  data () {
    return {
      activeItem: this.nodeType,
      scrollPos: 0,
      domElements: [],
      // this offset is dynamically changing based on the scroll position -> it allows highlighting even bottom elements when user scrolls to the end of the page
      scrollOffset: 150,
      scrollFromMenu: { navScroll: false, sectionGuid: '' },
      isScrolling: null,
      // Amount of pixels from top of the scrolled element - This makes the top element to be 150px from the top line of the window
      initialScrollOffset: 150,
      initialHeaderTop: 0,
      isHeaderFixed: false,
      intersectionObserver: null,
      dialogOpen: {
        type: String,
        default: null
      },
      saveCommand: undefined
    }
  },
  props: {},
  watch: {
    contentLanguage: function () {
      if (this.details.meta) {
        this.changeContentLanguage(this.contentLanguage)
      }
    }
  },
  mounted () {
    // Used to reset scroll position in order to prevent opening details with scroll from previous page
    window.scroll(0, 0)
  },
  created () {
    this.$nextTick(() => {
      if (this.domElements.length > 0) {
        this.indexDomElements()
      }
    }, 0)
    window.addEventListener('scroll', this.handleHeader)
    this.attachScrollListeners()
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleHeader)
    this.detachScrollListeners()
    this.intersectionObserver.disconnect()
  },

  computed: {
    ...mapGetters(['systemInfo', 'languages', 'uiLanguage', 'contentLanguage', 'definitionsByDefinitionGuid']),
    ...detailsStore.mapGetters(['details', 'validationErrors']),
    deleteLabel () {
      return this.$t('details.delete', { label: nodeTypeSingular[this.details.meta.nodeType] })
    },
    nameEditorCustomHeader () {
      return this.$t('details.rename', { label: nodeTypeSingular[this.details.meta.nodeType] })
    },
    meta () {
      return this.details.meta ? this.details.meta : {}
    },
    name () {
      return this.details.name ? this.details.name.value : ''
    },
    definitionName () {
      if (this.details.meta.definition) {
        return this.details.meta.definition.name
      } else {
        return this.$t('details.noDefinition')
      }
    },
    nodeType () {
      this.$nextTick(() => {
        this.indexDomElements()
        if (this.domElements.length === 1) {
          this.intersectionObserver.disconnect()
          this.detachScrollListeners()
        }
      }, 0)
      return this.details && this.details.meta ? this.details.meta.nodeType : null
    },
    anyValidationErrors () {
      return Object.getOwnPropertyNames(this.validationErrors || {}).filter(n => n !== '__ob__').length > 0
    },
    dynamicProperties () {
      const detailsComponent = SpecificDetails[this.details.meta.nodeType]
      if (!detailsComponent) return []
      const consumed = detailsComponent.consumedProperties.concat(['meta', 'sortOrder', 'name'])
      return Object.keys(this.details).filter(propertyName => !consumed.includes(propertyName) && this.details[propertyName].editor !== 'Price')
    },
    mediaType () {
      return this.details.primaryImage ? this.details.primaryImage.guid : this.details.media.guid
    }
  },
  components: {
    FormFeedback,
    UcSelectSimple,
    InfoWidget,
    ShortText,
    FieldsSection,
    LanguageSelector,
    BtnRound,
    IconMore,
    Simplebar,
    ...Editors,
    ...SpecificDetails,
    UcText,
    PropertyEditorDialog
  },
  methods: {
    ...detailsStore.mapActions(['changeDefinition', 'changeContentLanguage']),
    ...mapActions(['save', 'delete']),
    ...mapMutations(['setContentLanguage', 'addUndo', 'clearUndos']),
    ...builderStore.mapMutations(['setCurrentNodeName']),
    async saveChange (command) {
      this.enrichCommand(command)
      await this.save(command)
      this.$emit('change', command)
    },
    async submitCommand () {
      await this.saveChange(this.saveCommand)
      if (this.details['name'].errors) {
        return
      }
      this.closeDialogs()
      if (this.saveCommand.save.hasOwnProperty('name')) {
        this.setCurrentNodeName(this.saveCommand.save.name.value)
      }
    },
    onInput (saveCommand) {
      this.saveCommand = saveCommand
    },
    closeDialogs () {
      this.dialogOpen = null
    },
    openDialog (guid) {
      this.dialogOpen = guid
    },
    handleHeader () {
      let header = this.$refs.headerContainer
      if (this.initialHeaderTop === 0) {
        this.initialHeaderTop = header.getBoundingClientRect().top
      }
      if (header.getBoundingClientRect().top <= 0 && window.scrollY > this.initialHeaderTop) {
        header.classList.add('fixed')
        this.isHeaderFixed = true
      } else {
        header.classList.remove('fixed')
        this.isHeaderFixed = false
      }
    },
    enrichCommand (command) {
      const { meta } = command
      meta.contentLanguage = this.contentLanguage
      meta.nodeType = this.details.meta.nodeType
      meta.nodeGuid = this.details.meta.entityGuid
    },
    async deleteEntity () {
      const command = createSaveCommand('deleted', 'cbaf8c70-639c-4716-a891-00efb0fb82bc', 'Is Deleted', true, false)
      const { meta } = command
      meta.contentLanguage = this.contentLanguage
      meta.nodeType = this.details.meta.nodeType
      meta.nodeGuid = this.details.meta.entityGuid
      meta.survivesNodeChange = true
      meta.undoMessage = `${this.details.name.value} ${this.$t('global.wasDeleted')}`
      meta.undoAction = () => {
        this.$emit('resurrected', this.details.meta.entityGuid)
      }
      await this.delete(command)
      await this.$emit('deleted', meta.nodeGuid)
    },
    selectDefinition (definitionGuid) {
      const oldValue = this.details.meta.definition ? this.details.meta.definition.guid : null
      const command = createSaveCommand('definition', nodeTypeDefinitionGuidMap[this.details.meta.nodeType], 'definition', definitionGuid, oldValue)
      this.enrichCommand(command)
      this.changeDefinition(command)
    },
    scrollToElement (refName, isFromMenu) {
      if (isFromMenu) {
        this.scrollFromMenu.navScroll = true
        this.scrollFromMenu.sectionGuid = refName
      }
      let domElement = this.$refs[refName]
      if (Array.isArray(domElement)) {
        domElement = domElement[0].$el
      } else {
        domElement = this.$refs[refName].$el
      }
      let offset = domElement.getBoundingClientRect().top + window.scrollY - this.initialScrollOffset
      // Check if its possible to scroll the page that much, if not adjust the offset to scroll as much as possible to highlight the proper item -> limit is the maximum scroll position if the scroll position is unreachable, just set active class
      const limit = Math.max(document.body.scrollHeight, document.body.offsetHeight, document.documentElement.clientHeight, document.documentElement.scrollHeight, document.documentElement.offsetHeight)
      if (limit - window.innerHeight < offset) {
        this.setActiveNavField(refName)
      }
      window.scrollTo({ top: offset, behavior: 'smooth' })
    },
    isActiveNavField (menuItem) {
      return this.activeItem === menuItem
    },
    setActiveNavField (menuItem) {
      if (menuItem !== '') {
        this.activeItem = menuItem
      }
    },
    // Method for highlighting the current active element in the navigation
    onScrollMenuHighlight () {
      const fieldSection = document.getElementsByClassName('field-section-fields')[0]

      if (fieldSection === undefined) return
      // Condition to check if there is need to modify offset for finding closest element
      if (fieldSection.getBoundingClientRect().height - window.scrollY < window.innerHeight &&
          !this.scrollFromMenu.navScroll) {
        const scrollBarHeight = window.innerHeight * (window.innerHeight / (fieldSection.getBoundingClientRect().height + fieldSection.offsetTop))
        this.scrollOffset = window.innerHeight - (fieldSection.getBoundingClientRect().height - window.scrollY - scrollBarHeight)
        if (window.scrollY < this.initialScrollOffset) {
          this.scrollOffset = this.initialScrollOffset
        }
        // Else reset the offset back to the initial value
      } else {
        this.scrollOffset = this.initialScrollOffset
      }

      this.scrollPos = window.scrollY + this.scrollOffset
      const visibleElements = this.domElements.filter(x => x.intersecting)
      const closestElIndex = this.findClosestDomElementIndex(this.scrollPos, visibleElements)
      const el = visibleElements[closestElIndex]

      if (!this.scrollFromMenu.navScroll) {
        this.setActiveNavField(el.guid)
      } else {
        this.setActiveNavField(this.scrollFromMenu.sectionGuid)
      }
    },
    // Method for finding the closest element based on the scroll position - used for loop because of the performance (reduce takes twice as much time)
    findClosestDomElementIndex (scrollPos, collection) {
      let closestValue = Infinity
      let closestIndex = -1
      for (let i = 0; i < collection.length; i++) {
        const diff = Math.abs(collection[i].topPos - scrollPos)
        if (diff < closestValue) {
          closestValue = diff
          closestIndex = i
        }
      }
      return closestIndex
    },
    // Method for saving all sections into data, therefore it is possible to work with the data afterwards (for example highlight current active nav item)
    indexDomElements () {
      let options = {
        root: null, // relative to document viewport
        rootMargin: '-50px', // margin around root. Values are similar to css property. Unitless values not allowed
        threshold: 0 // visible amount of item shown in relation to root
      }
      this.intersectionObserver = new IntersectionObserver(this.observeElements, options)
      this.setActiveNavField(this.nodeType)
      this.domElements = []
      const collection = document.getElementsByClassName('fields-section')
      for (let i = 0; i < collection.length; i++) {
        collection[i].setAttribute('data-pos', collection[i].getBoundingClientRect().top.toString())
        const guid = collection[i].getAttribute('data-guid')
        const topPos = collection[i].getBoundingClientRect().y
        this.domElements.push({ guid: guid, topPos: topPos, intersecting: false })
        this.intersectionObserver.observe(collection[i])
      }
      // Needed to have at least one highlighted element
      this.domElements[0].intersecting = true
      this.attachScrollListeners()
    },
    observeElements (entries, observer) {
      for (let i = 0; i < entries.length; i++) {
        const domEL = this.domElements.find(x => x.guid === entries[i].target.getAttribute('data-guid'))
        domEL.intersecting = entries[i].isIntersecting
      }
    },
    attachScrollListeners () {
      // listener to auto-scroll the nav menu in case of many elements inside nav
      window.addEventListener('scroll', this.sectionNavAutoScroll)
      window.addEventListener('scroll', this.onScrollMenuHighlight)
    },
    detachScrollListeners () {
      window.removeEventListener('scroll', this.sectionNavAutoScroll)
      window.removeEventListener('scroll', this.onScrollMenuHighlight)
    },
    // Method for automatically scrolling the navbar in case there are that many properties that it requires vertical scrollbar
    sectionNavAutoScroll () {
      this.$nextTick(() => {
        window.clearTimeout(this.isScrolling)
        this.isScrolling = setTimeout(function () {
          this.scrollFromMenu.navScroll = false
          // Gets the active li and ul
          const activeElement = document.getElementsByClassName('active')[0]
          const sectionNavEl = document.querySelector('.section-nav-ul')

          if (typeof activeElement !== 'undefined' || typeof sectionNavEl !== 'undefined') {
          // Gets pixel position of active item
            const activeElOffset = activeElement.offsetTop
            // Gets last visible pixel
            const scrollPosition = sectionNavEl.scrollTop + sectionNavEl.clientHeight
            // Adding 10 pixel tolerance in case of the last element in the list
            const totalHeight = parseInt(window.getComputedStyle(activeElement).getPropertyValue('margin-top')) +
              parseInt(window.getComputedStyle(activeElement).getPropertyValue('margin-bottom')) +
              activeElement.clientHeight
            if (activeElOffset - scrollPosition > -totalHeight) {
            // ensures that the scroll always scrolls the element into visible part of the navScrollBar
              sectionNavEl.scrollTop = activeElOffset - sectionNavEl.clientHeight + totalHeight
            }
            if (activeElOffset < sectionNavEl.scrollTop) {
              sectionNavEl.scrollTop = activeElOffset
            }
          }
        // binds are necessary to preserve reactivity in event listener (the code is inside timeout)
        }.bind(this), 100)
      }, 0)
    }
  }
}
</script>

<style lang="stylus" scoped>
  .media-section
    display flex
    flex-direction row
    justify-content center
  .media-section > div + div
    margin-left: 20px
  .media-section.align-left
    justify-content left
  .details-view
    margin-bottom 60px
    overflow-y initial

  .details__static
    padding 35px 20px 20px 20px
    display grid
    grid-template-columns 1fr
    align-items center
    grid-row-gap 36px
  .details-content
    display flex
    margin 30px auto 0 auto
    justify-content flex-end
  .v-popover
    box(24px)
    margin-left 12px

  .validation.feedback
    font-size 1rem
    opacity 0.85
    color $col_black

    a, a:hover, a:active, a:visited
      color $col_black

    ul > li
      margin-bottom 8px

    ul > li > ul
      list-style none
      padding-left 0

  .section-nav-ul
    list-style none
    padding: 0 15px 0 0
    width 200px
    position: sticky
    top: 110px
    overflow-y auto
    overflow-x hidden
    max-height calc(100vh - 200px)
    scroll-behavior smooth
    margin: 0
    &::-webkit-scrollbar
     width: 0 !important
  .section-nav-ul
    scrollbar-width: none
  .section-nav-ul
    -ms-overflow-style none
  .section-nav:hover
    cursor pointer
    primary-body-text-bold()

  .section-nav
    padding-left 10px
    primary-body-text()
    color: $secondary-text
    margin-bottom 15px
    position relative

  .section-nav.active
    primary-body-text-bold()

  .section-nav.active:before
    border-left 1px solid $primary-text
    content ' '
    margin-right 10px
    width 10px
    height 100%
    position absolute
    left 0

  .field-section-fields
    width 100%
    margin-bottom 100px

  // Header styling
  .details-title
    primary-heading-1()
    max-width 500px

  .details-subheader
    secondary-body-text()
    max-width 500px
    margin 0
    &:not(:last-child)
      margin-bottom 4px

  .details-header-placeholder
    height 80px

  .header-top
    display flex
    align-items center
    justify-content space-between
    height 40px
    margin-bottom 2px

  .fixed
    position fixed
    top 0
    background-color #f9f9f9
    opacity 0.9
    width 1000px
    z-index 100
    height 80px
    display: flex;
    flex-direction: column;
    justify-content: center;
    & .details-title
      primary-body-text-bold()

  .name-group
    display flex
    align-items center
  .more-button
    margin-left 7px

  .action
    action()

  .audit-item + .audit-item
    margin-top 20px

  .separator-line
    separator-line()
    width calc(100% + 60px)
    margin 30px 0 30px -30px

  .audit-header
    secondary-petit-text-bold()

  .audit-text
    secondary-petit-text()
</style>
