<template>
  <label class="uc-toggle switch" :class="{readonly}">
    <input type="checkbox" :name="id" :id="id" :disabled="readonly" :checked="checkedValue" :value="guid" @change="onChange" v-show="!compact">
    <span class="slider round"></span>
    <span v-truncate class="label" v-if="!compact">{{label}}</span>
  </label>
</template>

<script>
import GetId from '../../js/id-generator.js'
export default {
  name: 'uc-toggle',
  data () {
    return {
      id: null
    }
  },
  props: {
    label: String,
    readonly: Boolean,
    checked: Boolean,
    guid: String,
    tooltip: String,
    compact: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    checkedValue () {
      return this.checked ? 'checked' : ''
    }
  },
  methods: {
    onChange (event) {
      this.$emit('change', { value: event.target.checked, guid: this.guid })
    }
  },
  created () {
    this.id = GetId()
  }
}
</script>

<style lang="stylus" scoped>
.uc-toggle
  cursor pointer
  position: relative
  display flex
  align-items center
  &.readonly
    cursor not-allowed
.switch
  position relative
  display flex
  height 24px
  min-width 40px
  &.readonly
    .slider
      cursor not-allowed
  input
    opacity 0
    width 0
    height 0
.slider
  display flex
  cursor pointer
  height 100%
  width 40px
  background-color #ccc
  -webkit-transition .4s
  transition .4s
  &:before
    position absolute
    content ""
    height 20px
    width 20px
    left 2px
    bottom 2px
    background-color white
    -webkit-transition .4s
    transition .4s
  &.round
    border-radius 12.8px
    &:before
      border-radius 50%
input
  &:checked
    & ~ .slider
      background-color $ucommerce-blue
      &:before
        -webkit-transform translateX(17px)
        -ms-transform translateX(17px)
        transform translateX(17px)
  &:focus
    & ~ .slider
      box-shadow 0 0 1px #2196F3
.label
  max-width 700px
  primary-body-text()
</style>
