<template>
  <div>
<!--    <div class="properties_title">{{ title }}</div>-->
    <div class="properties_list">
        <div v-for="property in properties" :key="property.index" class="property">
          <uc-checkbox :label="property.label" :checked="property.show" @change="toggleColumn(property)"/>
          <span class="property_definition">
            <info-icon v-if="property.definitions.length > 1" class="icon">
              Part of definition family: {{ property.definitions.toString() }}
            </info-icon>
            <span v-else>{{ property.definitions[0]}}</span>
          </span>
        </div>
    </div>
  </div>
</template>

<script>

import UcCheckbox from '../../../shared/components/forms/uc-checkbox'
import InfoIcon from '../../../shared/icons/info'
import { createNamespacedHelpers } from 'vuex'
const genericListStore = createNamespacedHelpers('genericList')

export default {
  name: 'PropertiesList',
  components: {
    UcCheckbox,
    InfoIcon

  },
  props: {
    title: String,
    properties: Array,
    onClickAll: Boolean,
    onClickSingle: Boolean,
    listId: {
      type: String,
      required: true
    }
  },
  methods: {
    ...genericListStore.mapMutations(['toggleListColumn']),
    toggleColumn (property) {
      const show = (this.onClickSingle) ? !property.show : this.onClickAll
      this.toggleListColumn({ id: this.listId, index: property.index, show: show })
    },
    getCheckClassObject: function (property) {
      return {
        'added': property !== undefined ? property.show : false
      }
    }
  }
}
</script>

<style lang="stylus" scoped>

@-webkit-keyframes fadeInProperty {
    0%   { opacity: 0 }
    100% { opacity: 1 }
}

.properties_title
  padding 6px 24px
  background-color $col_grey1

.properties_list
  overflow hidden
  // padding 8px 0

.property
  position relative
  display flex
  padding 10px 30px

  -webkit-animation ease alternate
  -webkit-animation-name fadeInProperty
  -webkit-animation-duration 250ms
  &:hover
    background-color $col_hover2

.property_definition
  margin-left auto
  color #b9babb

.icon
  circle(16px)
  display flex
  svg
    fill rgba(0,0,0,0.2)
    margin auto
  &:hover
    background-color $col_hover1

/deep/ label.uc-checkbox
  truncate()
</style>
