<template>
  <div class="details-product">
    <h2 class="basic-info-heading">{{ $tc('details.basicInformation') }}</h2>
    <section class="details-product__static">
      <div class="details-product__static-right">
        <uc-boolean :definition="details.displayOnWebsite" name="displayOnWebsite" @change="save"></uc-boolean>
        <uc-short-text v-if="language" :showLanguage="true" :language="language" :definition="details.displayName" name="displayName" @change="save" />
        <uc-short-text :definition="details.sku" name="sku" @change="save" />
        <long-text v-if="language" :compact="false" :definition="details.shortDescription" :language="language" name="shortDescription" @change="save"></long-text>
        <uc-rich-text v-if="language" :definition="details.longDescription" name="longDescription" :language="language" @change="save"></uc-rich-text>
      </div>
    </section>
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
import UcShortText from '../../../shared/components/editors/uc-short-text'
import LongText from '../../../shared/components/editors/uc-long-text'
import UcRichText from '../../../shared/components/editors/uc-rich-text'
import UcBoolean from '../../../shared/components/editors/uc-boolean'
const { mapGetters } = createNamespacedHelpers('details')
export default {
  name: 'product-details',
  props: {
    language: String
  },
  computed: {
    ...mapGetters(['details'])
  },
  consumedProperties: ['variantSku', 'key', 'sku', 'displayName', 'name', 'shortDescription', 'longDescription', 'displayOnWebsite', 'createdBy', 'createdOn', 'modifiedBy', 'modifiedOn', 'guid', 'primaryImage', 'thumbnailImage', 'definition', 'rating'],
  components: {
    LongText,
    UcShortText,
    UcRichText,
    UcBoolean
  },
  methods: {
    save (e) {
      this.$emit('change', e)
    }
  }
}
</script>

<style lang="stylus" scoped>
.basic-info-heading
  primary-heading-2()
  margin 0
.details-product__static
  display flex
  align-items flex-start
  justify-content space-between

.details-product__static-right
  width: 100%
  display grid
  grid-template-rows 3
  grid-gap 20px

.primaryImage
  margin-bottom 25px

</style>
