<template>
  <media
    :definition="definition"
    :name="name"
    :compact="compact"
    :multi-select="false"
    :language="language"
    content-type="content"
    @change="change"/>
</template>

<script>
import Media from './uc-image-picker'
export default {
  name: 'uc-email-content',
  components: {
    Media
  },
  props: {
    name: String,
    definition: Object,
    compact: Boolean,
    language: String
  },
  methods: {
    change (command) {
      this.$emit('change', command)
    }
  }
}
</script>

