import * as types from './mutation-types'
import { distinctByGuid } from '../../../shared/js/distinct'

const mutations = {
  [types.SET_SECTIONS_IN_TREE] (state, { position, cat, elements, categoriesCount }) {
    if (position < 0) { // no rootnode
      state.treeSections = []
    }
    const newSection = {
      categories: elements,
      position: position,
      endOfList: elements.length >= categoriesCount || elements.length === 0,
      categoriesCount: categoriesCount,
      ...cat
    }
    state.displayedNode.hasChildren = newSection.categories.length > 0
    if (position === state.treeSections.length) { // Leaf node selected
      state.treeSections.push(newSection)
      return
    }
    state.treeSections[position] = newSection
    state.treeSections = state.treeSections.slice(0, position + 1)
  },

  [types.ADD_ITEMS_TO_SECTION_IN_TREE] (state, { cat, elements, categoriesCount }) {
    cat.categoriesCount = categoriesCount
    cat.categories = distinctByGuid([...cat.categories, ...elements])
    cat.endOfList = cat.categories.length >= categoriesCount || elements.length === 0
  },

  [types.SELECT_NODE] (state, { parentGuid, catGuid }) {
    // Only the first section (catalogs) do not have a guid
    const section = parentGuid ? state.treeSections.find(x => x.guid === parentGuid) : state.treeSections[0]
    section.categories.forEach(cat => {
      if (cat.guid === catGuid) {
        cat.selected = true
        cat.displaying = true
        if (state.displayedNode !== cat) {
          state.displayedNode.displaying = false
        }
        state.displayedNode = cat
      } else {
        cat.selected = false
      }
    })
  },

  [types.RESET_DISPLAYED] (state) {
    state.displayedNode = {}
  },

  [types.SET_SECTION_WIDTH] (state, width) {
    state.sectionWidth = width
  },

  [types.SET_SECTION_HEIGHT] (state, height) {
    state.sectionHeight = height
  },

  [types.SET_DRAG_INFO] (state, info) {
    state.dragInfo = info
  },

  [types.SET_DRAG_SECTION_GUID] (state, guid) {
    state.dragInfo.currentSectionGuid = guid
  },

  [types.END_DRAG] (state) {
    state.dragInfo.dragging = false
  },

  [types.MOVE_DRAGGED_NODE] (state, { parentGuid, newIndex }) {
    const fromSection = state.treeSections.find(x => x.guid === state.dragInfo.parentGuid)
    const toSection = state.treeSections.find(x => x.guid === parentGuid)
    if (!toSection && !fromSection) return // from section no longer visible, to section not expanded
    if (!toSection) { // dropped on a node that is not selected/expanded
      fromSection.categories.splice(state.dragInfo.catIndex, 1)
      return
    }
    if (!fromSection) { // from section replaced by other
      if (newIndex === -1) { // if no index provided we push to the end
        toSection.categories.push(state.dragInfo.node)
      } else {
        toSection.categories.splice(newIndex, 0, state.dragInfo.node)
      }
      return
    }
    if (newIndex === -1) {
      toSection.categories.push(fromSection.categories.splice(state.dragInfo.catIndex, 1)[0])
    } else {
      toSection.categories.splice(newIndex, 0, fromSection.categories.splice(state.dragInfo.catIndex, 1)[0])
    }
  },

  [types.SET_SELECTED_STORE] (state, store) {
    state.selectedStore = store.guid
  },

  [types.SET_STORES] (state, stores) {
    state.stores = stores
  },

  [types.SET_CURRENT_NODE_NAME] (state, name) {
    state.displayedNode.name = name
  }
}

export default mutations
