<template>
  <div class="uc-text-area uc-editor" v-bind:class="[readonly ? 'readonly': '', focusOn ? 'active': '']">
    <div class="textarea-field-container">
      <label v-on:click="setFocusAndFocus()" v-if="label">{{label}}</label>
      <div class="textarea-field-wrapper">
          <div class="feedback-icon-wrap">
            <div class="feedback-icon">
              <uc-icon v-show="condition === 'Active'" icon="ArrowBack"></uc-icon>
              <uc-icon v-show="this.isError" icon="Asterix"></uc-icon>
            </div>
          </div>
          <textarea ref="area" class="uc-text-area__area" rows="5" :id="id" :disabled="readonly" :value="value"
          @input="emitValue"
          @focus="onFocus"
          @blur="onBlur"></textarea>
      </div>
    </div>
  </div>
</template>

<script>
import getID from '../../js/id-generator'
import ucIcon from '../../../shared/icons/uc-icon'
export default {
  name: 'uc-text-area',
  components: {
    ucIcon
  },
  data () {
    return {
      id: null,
      focusOn: false,
      inputValue: '',
      condition: 'Idle'
    }
  },
  props: {
    label: String,
    placeholder: String,
    value: String,
    isError: {
      type: Boolean,
      default: () => false
    },
    readonly: Boolean,
    error: String,
    tooltip: String
  },
  methods: {

    getFocus () {
      return (this.value === '' || this.inputValue === '')
    },

    emitValue (event) {
      this.$emit('input', event.target.value)
      this.inputValue = event.target.value
    },
    onBlur (event) {
      this.focusOn = this.inputValue !== '' || this.value !== ''
      this.condition = this.value === '' ? 'Idle' : 'Filled'
      this.$emit('blur', event)
    },

    onFocus (event) {
      this.$emit('focus', event)
      this.setFocus(event)
    },

    setFocus (event) {
      if (!this.readonly) {
        this.focusOn = true
        this.condition = 'Active'
      } else {
        this.focusOn = this.getFocus()
      }
    },

    setFocusAndFocus () {
      this.focusOn = true
      this.$refs.area.focus()
    }

  },
  created () {
    this.id = getID()
  },
  mounted () {
    this.focusOn = this.value
  }
}
</script>

<style lang="stylus" scoped>
.uc-editor
  margin-top 15px

.uc-text-area
  position relative
  &.active
    label
      transform translateY(-10px)
      transition-duration 200ms
      background-color #fff
      line-height 20px
      font-size 12px
      padding 0 4px
    textarea
      &:focus
        color: $ucommerce-editors-active-input-color

  &.readonly
    textarea
      background-color #f7f7f9
      color #cacad7

    .textarea-field-container
      .textarea-field-wrapper
        .uc-text-area__area
          -moz-user-select none
          -webkit-user-select none
          -khtml-user-select none
          user-select none
        background-color #f7f7f9

    label
      background-color #f7f7f9

    .input-field-wrapper
      background-color: $ucommerce-platform-bg

  label
    position absolute
    color $ucommerce-secondary
    line-height 50px
    left 14px
    font-size 16px
    padding 0 4px
    background-color: transparent
    z-index 500
    -webkit-touch-callout none
    -webkit-user-select none
    -khtml-user-select none
    -moz-user-select none
    -ms-user-select none
    user-select none

  textarea
    resize none
    padding 16px

  textarea:focus, input:focus, input[type]:focus
    outline 0 none

  .textarea-field-container
    border-radius 4px
    background-color #e1e1e8

    .feedback-icon-wrap
      position relative
      .feedback-icon
        position absolute
        z-index 200
        right 16px
        margin-top 8px

    .textarea-field-wrapper
      border-radius 4px
      background-color #fff
      border 1px #e1e1e8 solid
      padding 1px

      .uc-text-area__area
        font-size 16px
        position relative
        width 100%
        min-height 50px
        border 0
        line-height 1.38
        padding-right 52px

      .uc-textarea-area__pullWrapper
        position relative
        display inline-block

        .uc-textarea-area__pull
          height 0
          width 0
          border-top 14px solid #e1e1e8;
          border-left 14px solid transparent;
          border-right 14px solid transparent;
          -webkit-transform rotate(-45deg)
          position absolute
          bottom -3px
          right -10px
          pointer-events none
          z-index 2

</style>
