
function truncate (el, binding, vnode) {
  el.classList.add('truncated-el')

  if (el.offsetWidth < el.scrollWidth) {
    el.title = el.innerText
  }
}

export default {
  componentUpdated (el, binding, vnode) {
    truncate(el, binding, vnode)
  },
  inserted (el, binding, vnode) {
    truncate(el, binding, vnode)
  }
}
