<template>
  <div class="uc-number-editor uc-editor" :class="{focused, compact}">
    <template v-if="compact">
      <label>{{ definition.value }}</label>
    </template>
    <template v-else>
      <uc-numeric-input
        :value="definition.value"
        :label="computedLabel"
        :placeholder="definition.placeholder"
        :error="definition.error"
        :readonly="definition.readonly"
        :tooltip="definition.description"
        @blur="onBlur"
        @focus="onFocus"
        @input="onInput"/>
      <feedback v-for="error in definition.errors" :value="error" :key="error" type="error"/>
    </template>
  </div>
</template>

<script>
import feedback from '../widgets/field-feedback'
import createSaveCommand from '../../js/create-save-command'
import UcNumericInput from '../forms/uc-numeric-input'
import debounce from 'debounce'

const emitChange = function (newValue) {
  if (this.definition.value !== newValue) {
    const saveCommand = createSaveCommand(this.name, this.definition.guid, this.definition.label, newValue, this.definition.value)
    this.$emit('change', saveCommand)
  }
}

export default {
  name: 'number',
  data () {
    return {
      focused: false
    }
  },
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {}
      }
    },
    compact: {
      type: Boolean,
      default: false
    },
    language: String
  },
  computed: {
    computedLabel () {
      const lang = this.definition.multilingual ? `(${this.language})` : ''
      return `${this.definition.label} ${lang}`
    }
  },
  components: {
    UcNumericInput,
    feedback
  },
  methods: {
    onInput (newValue) {
      this.emitChangeDebounced(newValue)
    },
    onBlur (e) {
      this.emitChange(e.target.value)

      if (e.target.value !== '' || this.definition.value !== null) {
        this.focused = true
      }
    },
    onFocus () {
      this.focused = true
    },
    emitChange: emitChange,
    emitChangeDebounced: debounce(emitChange, 400)
  },
  mounted () {
    this.focused = this.definition.value
  }
}
</script>

<style lang="stylus" scoped>
.uc-editor
  margin-top 15px
.compact
  margin-top 0
</style>
