export const values = map => Object.keys(map).map(k => map[k])
export const distinctBy = (data, func) => {
  const d = data.reduce((acc, cur) => {
    acc[func(cur)] = cur
    return acc
  }, {})
  return values(d)
}
export const distinctByEntityGuid = data => distinctBy(data, d => d.meta.entityGuid)
export const distinctByGuid = data => distinctBy(data, d => d.guid)
