<template>
  <div class="list-wrapper">
    <section class="list-container" v-show="!entity">
      <header class="list-header-area">
        <section class="header-left">
          <span class="list-heading">{{ $tc('list.products') }}</span>
          <span class="item-count" v-if="totalCount > 0">({{ totalCount }})</span>
        </section>
        <section class="header-right">
          <uc-button text="Create" :primary="true"></uc-button>
          <settings :listId="id"></settings>
        </section>
      </header>
      <list-table :list="currentList" ref="listTable"
        :totalCount="totalCount"
        @needsMoreData="loadMoreData"
        @columnsDragged="columnsDragged"
        @rowClick="openEntityEditor"
        @sortBy="sortBy"></list-table>
    </section>
    <section v-if="entity" class="details dialog">
      <div class="details-content">
        <header @click="entity = null">
          <arrow-forward class="back-icon"></arrow-forward>
          <span class="back-label">{{ $tc('global.back') }}</span>
        </header>
        <generic-details @deleted="deleteEntityAndCloseDetails"></generic-details>
      </div>
    </section>
  </div>
</template>

<script>
import MapMetadata from '../../js/map-metadata-response.js'
import GetId from '../../../shared/js/id-generator.js'
import UcButton from '../../../shared/components/forms/uc-button'
import Settings from './settings'
import ListTable from './list-table.vue'
import ArrowForward from '../../../shared/icons/arrow-forward-1.vue'
import GenericDetails from '../details-view/generic-details'

import { makeDefinitionsByDefinitionGuid, mergeAllData } from '../../js/merge-definitions-and-data'
import { createNamespacedHelpers, mapGetters, mapMutations } from 'vuex'
import setUrlParam from '../../../shared/js/set-url-param'
const genericListStore = createNamespacedHelpers('genericList')
const detailsStore = createNamespacedHelpers('details')

export default {
  name: 'List',
  components: {
    UcButton,
    Settings,
    ListTable,
    ArrowForward,
    GenericDetails
  },
  props: {
    id: {
      type: String,
      required: true
    },
    metadataUrl: {
      type: String,
      required: true
    },
    dataUrl: {
      required: true
    },
    defaultColumnsConfiguration: {
      type: Object,
      required: false
    }
  },
  data () {
    return {
      isLoadingItems: false,
      entity: undefined,
      totalCount: -1
    }
  },
  created () {
    this.addListId(this.id)
    this.setListLocalStorageKey({ id: this.id, key: this.getLocalStorageKey('layout1') })
  },
  watch: {
    async dataUrl () {
      if (this.dataUrl) {
        await this.loadMetadata()
        this.$refs.listTable.initColumnsConfiguration(this.defaultColumnsConfiguration, 'width')
        const rows = await this.loadData()
        if (rows) {
          this.setListRows({ id: this.id, rows: mergeAllData(this.definitionsByDefinitionGuid, rows) })
        }
      }
    },
    contentLanguage () {
      this.setDetails(this.entity)
    }
  },
  computed: {
    ...genericListStore.mapGetters(['lists']),
    ...mapGetters(['contentLanguage', 'definitionsByDefinitionGuid']),
    currentList () {
      return this.lists[this.id]
    }
  },
  methods: {
    ...genericListStore.mapMutations([
      'addListId',
      'setListLocalStorageKey',
      'setListLastModified',
      'setListColumns',
      'setListRows',
      'addListRows'
    ]),
    ...mapMutations(['setDefinitionsByDefinitionGuid']),
    ...detailsStore.mapMutations(['setDetails']),
    ...genericListStore.mapActions(['applyColumnOrder']),
    async loadMetadata () {
      let lastModified = this.currentList.lastModified
      try {
        const result = await this.axios.get(this.metadataUrl, { headers: { 'If-Modified-Since': lastModified } })
        this.setDefinitionsByDefinitionGuid(makeDefinitionsByDefinitionGuid(result.data))
        this.setListLastModified({ id: this.id, lastModified: result.headers['last-modified'] })
        let uniqueColumns = this.groupDefinitionFields(result.data)
        let mappedColumns = MapMetadata(uniqueColumns, GetId())
        this.setListColumns({
          id: this.id,
          columns: mappedColumns,
          defaultColumnNames: Object.keys(this.defaultColumnsConfiguration),
          initDefaultColumns: true })
        this.applyColumnOrder({ id: this.id, currentList: this.currentList })
        return result.data
      } catch (e) {
        if (!e.response || e.response.status !== 304) {
          throw e
        }
      }
    },
    async loadData () {
      if (this.isLoadingItems) return
      this.isLoadingItems = true
      try {
        const result = await this.axios.get(this.dataUrl)
        const mappedResult = this.getFieldsExceptOf(result.data, '')
        this.totalCount = +result.headers['x-total-count'] || 0
        this.setListRows({ id: this.id, rows: mappedResult })
        this.$nextTick(() => this.$refs.listTable.checkIfMoreItemsNeeded())
        return result.data
      } finally {
        this.isLoadingItems = false
      }
    },
    async loadMoreData () {
      if (this.isLoadingItems) return
      if (this.currentList.rows.length >= this.totalCount) return
      this.isLoadingItems = true
      try {
        let urlWithPagination = setUrlParam(this.dataUrl, 'limit', 100)
        urlWithPagination = setUrlParam(urlWithPagination, 'startAt', this.currentList.rows.length)
        const result = await this.axios.get(urlWithPagination)
        const mappedResult = this.getFieldsExceptOf(result.data, '')
        this.totalCount = +result.headers['x-total-count'] || 0
        const rowsWithDefinitions = mergeAllData(this.definitionsByDefinitionGuid, mappedResult)
        this.addListRows({ id: this.id, rows: rowsWithDefinitions })
        this.$nextTick(() => this.$refs.listTable.checkIfMoreItemsNeeded())
        return result.data
      } finally {
        this.isLoadingItems = false
      }
    },
    sortBy (columnKey) {
      this.$emit('sortBy', columnKey)
    },
    columnsDragged (columns) {
      this.setListColumns({ id: this.id, columns: columns })
    },
    groupDefinitionFields (definitions) {
      let map = new Map()
      definitions.forEach(definition => {
        const fields = this.getFieldsExceptOf(definition, '')
        fields.forEach(field => {
          let x = { ...field }
          if (!map.has(x.key)) {
            x.definitions = []
          } else {
            x = map.get(x.key)
          }
          let definitionName = definition.definition.name
          if (definitionName) {
            x.definitions.push(definitionName)
          }
          map.set(x.key, x)
        })
      })
      return [...map.values()]
    },
    getFieldsExceptOf (definition, field) {
      let fieldsKeys = Object.keys(definition).filter(x => x !== field)
      let fieldsObj = fieldsKeys.reduce((obj, key) => ({ ...obj, [key]: definition[key] }), {})
      fieldsKeys.forEach(key => {
        fieldsObj[key].key = key
      })
      return Object.values(fieldsObj)
    },
    getLocalStorageKey (layoutName) {
      return `uc-list-setting-${this.id}-${layoutName}`
    },
    openEntityEditor (row) {
      this.entity = row
      this.setDetails(row)
    },
    deleteEntityAndCloseDetails (guid) {
      let newList = this.currentList.rows.filter(x => x.meta.entityGuid !== guid)
      this.setListRows({ id: this.id, rows: newList })
      this.totalCount = newList.length
      this.openEntityEditor(null)
    }
  }
}
</script>

<style lang="stylus" scoped>
.list-wrapper
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1)
  background-color #fff
.list-container
  display flex
  flex-direction column
  justify-content center
  margin 0 auto
  background #ffffff
  padding 0
  overflow-x auto

.list-header-area
  display flex
  align-items center
  justify-content space-between
  margin 24px 20px
  & > section
    display flex
    flex-direction row
    & > .uc-button
      margin-right 16px

  .list-heading
    primary-heading-2()
  .item-count
    primary-heading-2()
    font-weight normal
    margin-left 10px

.details.dialog
  display flex
  flex-direction column
  position absolute
  top 0
  left 0
  right 0
  z-index 1000
  background-color $platform-bg
.details-content
  width 1000px
  margin 0 auto
  header
    margin-top 30px
    cursor pointer
    width fit-content
    position relative
    z-index 1000
    & > .back-icon
      box(12px)
      transform rotate(180deg)
    & > .back-label
      primary-details-text()
      margin-left 6px
</style>
