<template>
    <div class="uc-text-input" v-bind:class="[readonly ? 'readonly': '', focusOn ? 'active': '', size === 'big' ? 'big': '']">
        <div class="input-field-container">
            <label v-on:click="setFocusAndFocus()">{{label}}</label>
            <div class="input-field-wrapper">
                <div class="feedback-icon-wrap">
                    <div class="feedback-icon">
                        <uc-icon v-show="!this.isError && condition === 'Active'" icon="ArrowBack"></uc-icon>
                        <uc-icon v-show="this.isError" icon="Asterix"></uc-icon>
                    </div>
                </div>
                <input ref="field" type="text" :disabled="readonly" @input="emitValue" :value="value" @focus="setFocus" @blur="setBlur"/>
            </div>
        </div>
    </div>
</template>

<script>
import getID from '../../js/id-generator'
import ucIcon from '../../../shared/icons/uc-icon'

export default {
  name: 'uc-text-input',
  data () {
    return {
      id: null,
      focusOn: false,
      inputValue: '',
      condition: 'Idle'
    }
  },
  props: {
    label: String,
    placeholder: String,
    value: String,
    readonly: Boolean,
    isError: {
      type: Boolean,
      default: () => false
    },
    error: String,
    tooltip: String,
    size: {
      type: String,
      default: () => 'small'
    }
  },
  components: {
    ucIcon
  },
  methods: {
    getFocus () {
      return (this.value !== '' || this.inputValue !== '' || this.isError)
    },

    setFocus () {
      if (!this.readonly) {
        this.focusOn = true
        this.condition = 'Active'
      } else {
        this.focusOn = this.getFocus()
      }
    },

    setBlur (event) {
      this.focusOn = this.inputValue !== '' || this.isError
      this.condition = this.inputValue === '' ? 'Idle' : 'Filled'
      this.$emit('blur', event)
    },

    emitValue (event) {
      this.$emit('input', event.target.value)
      this.inputValue = event.target.value
    },

    setFocusAndFocus () {
      this.focusOn = true
      this.$refs.field.focus()
    },

    setActiveFocus () {
      let focusTimer = setTimeout(() => {
        this.setFocusAndFocus()
        clearTimeout(focusTimer)
      }, 50)
    }
  },
  created () {
    this.id = getID()
    this.inputValue = this.value
  },
  mounted () {
    this.focusOn = this.getFocus()
  }
}
</script>

<style lang="stylus" scoped>

    .uc-text-input{

        font-size 16px
        position relative

        label {
             position absolute
             color $ucommerce-secondary
             line-height 50px
             left: 14px
             font-size: 16px
             padding 0 4px
             background-color: transparent
             removeUserSelect()
         }

        input {
            width: calc(100% - 36px);
            height: 50px;
            background-color: transparent;
            color: $ucommerce-primary;
            border: none;
            font-size: 16px;
            padding: 0 16px 0 16px

            &::-ms-clear {
                display: none;
            }
        }

        textarea:focus, input:focus, input[type]:focus {
            // border-color: rgba(229, 103, 23, 0);
            // box-shadow: 0 1px 1px rgba(229, 103, 23, 0.075) inset, 0 0 8px rgba(229, 103, 23, 0.6);
            outline: 0 none;
        }

        .feedback-icon-wrap {
            position relative
            .feedback-icon {
                position absolute
                z-index 200
                right 16px
                margin-top 8px
            }
        }

        .input-field-container{
            border-radius 4px
            background-color #e1e1e8

            .input-field-wrapper{
                border-radius 4px
                background-color #fff
                border: 1px #e1e1e8 solid;
                overflow: hidden;
            }

            &.disabled {
                background-color $ucommerce-secondary
            }

            &.danger {
                background-color $col_red
            }
        }

        &.big {
            input {
                width: calc(100% - 32px);
            }
            width : 100%;
        }

        &.active {
            label{
                transform: translateY(-10px)
                transition-duration: 200ms
                background-color: #fff
                line-height: 20px
                font-size: 12px
                padding: 0 2px 0 3px
            }
            input {
                &:focus{
                    // box-shadow: 0 1px 1px rgba($ucommerce-secondary, 0.075) inset, 0 0 8px rgba($ucommerce-secondary, 0.6);
                    //
                    color: $ucommerce-editors-active-input-color
                }
            }
        }

        &.readonly {

            background-color: #f7f7f9
            input {
                background-color: #f7f7f9
                color: #cacad7
            }

            label {
                background-color: #f7f7f9
            }

            .input-field-wrapper {
                background-color: #f7f7f9
            }
        }
    }
</style>
