import * as types from './mutation-types'

const mutations = {
  [types.SET_SECTIONS_IN_TREE] (state, { position, cat, elements, allElements, categoriesCount }) {
    if (position < 0) { // no rootnode
      state.treeSections = []
    }
    const newSection = {
      categories: elements,
      allCategories: allElements,
      position: position,
      endOfList: elements.length >= categoriesCount || elements.length === 0,
      categoriesCount: categoriesCount,
      ...cat
    }
    state.displayedNode.hasChildren = newSection.categories.length > 0
    if (position === state.treeSections.length) { // Leaf node selected
      state.treeSections.push(newSection)
      return
    }
    state.treeSections[position] = newSection
    state.treeSections = state.treeSections.slice(0, position + 1)
  },

  [types.ADD_ITEMS_TO_SECTION_IN_TREE] (state, { cat, elements, categoriesCount }) {
    cat.categoriesCount = categoriesCount
    cat.categories = [...cat.categories, ...elements]
    cat.endOfList = cat.categories >= categoriesCount || elements.length === 0
  },

  [types.SELECT_NODE] (state, { parentGuid, catGuid }) {
    // Only the first section (catalogs) do not have a guid
    const section = parentGuid ? state.treeSections.find(x => x.guid === parentGuid) : state.treeSections[0]
    section.categories.forEach(cat => {
      if (cat.guid === catGuid) {
        cat.selected = true
        cat.displaying = true
        if (state.displayedNode !== cat) {
          state.displayedNode.displaying = false
        }
        state.displayedNode = cat
      } else {
        cat.selected = false
      }
    })
  },

  [types.RESET_DISPLAYED] (state) {
    state.displayedNode = {}
  },

  [types.SET_SECTION_WIDTH] (state, width) {
    state.sectionWidth = width
  },

  [types.SET_SECTION_HEIGHT] (state, height) {
    state.sectionHeight = height
  },

  [types.SET_SELECTED_ITEMS] (state, items) {
    state.selectedItems = items.filter(x => x && x !== '')
  },
  [types.TOGGLE_SELECTED_ITEM] (state, itemGuid) {
    if (state.selectedItems.find(x => x === itemGuid)) {
      state.selectedItems = state.selectedItems.filter(x => x !== itemGuid)
    } else {
      state.selectedItems.push(itemGuid)
    }
  }
}

export default mutations
