<template>
  <div class="uc-select-simple" :class="{readonly, droppable: hasOptions }">
    <div class="uc-select-simple__selected" @click="toggleOptions">{{selectedText}}</div>
    <div class="uc-select-simple__arrow" :class="{toggled: optionsToggled}" @click="toggleOptions" v-if="hasOptions"><triangle></triangle></div>
    <transition name="dropdown">
      <ul v-show="showOptions" class="uc-select-simple__options dropdown-overlay">
        <li class="uc-select-simple__option" v-for="option in filteredOptions" :key="option.value" @click="selectOption(option)">{{option.text}}</li>
      </ul>
    </transition>
  </div>
</template>

<script>
import Triangle from '../../icons/triangle-right.vue'
export default {
  name: 'uc-select-simple',
  data () {
    return {
      optionsToggled: false
    }
  },
  props: {
    options: {
      type: Array
    },
    selected: String,
    placeholder: {
      type: String,
      default: function () {
        return this.$t('global.selectPlaceholder')
      }
    },
    noOptionsPlaceholder: {
      type: String,
      default: function () {
        return this.$t('global.selectNoOptionsPlaceholder')
      }
    },
    readonly: Boolean
  },
  computed: {
    selectedOption () {
      return this.options.find(x => x.value === this.selected)
    },
    selectedText () {
      if (this.selectedOption) {
        return this.selectedOption.text
      }
      if (this.hasOptions) {
        return this.placeholder
      } else {
        return this.noOptionsPlaceholder
      }
    },
    hasOptions () {
      return this.options.length > 0
    },
    showOptions () {
      return this.optionsToggled && !this.readonly && this.hasOptions
    },
    filteredOptions () {
      return this.options.filter(option => option !== this.selectedOption)
    }
  },
  methods: {
    hide () {
      this.optionsToggled = false
    },
    toggleOptions (event) {
      if (this.readonly) return
      event.preventDefault()
      this.optionsToggled = !this.optionsToggled
      if (this.optionsToggled) {
        window.setTimeout(() => {
          window.addEventListener('click', this.closeHandler)
        }, 10)
      }
    },
    selectOption (option) {
      if (this.readonly) return
      this.$emit('selected', option.value)
      this.optionsToggled = false
    },
    closeHandler (event) {
      this.optionsToggled = false
      window.removeEventListener('click', this.closeHandler)
    }
  },
  components: {
    Triangle
  }
}
</script>

<style lang="stylus">
.uc-select-simple
  display inline-block
  font-size 0.875rem
  &.droppable
    cursor pointer
  position relative
  white-space nowrap
  &.readonly
    cursor not-allowed

.uc-select-simple__label
  display block

.uc-select-simple__selected
  display inline-block
  white-space nowrap

.uc-select-simple__arrow
  display inline-block
  padding 0 4px
  svg
    box(6px)
    transition transform 200ms ease
    transform rotate3d(0, 0, 1, 90deg)
  &.toggled
    svg
      transform rotate3d(0, 0, 1, -90deg)

.uc-select-simple__options
  zero-list()
  color $base_text_col
  margin-top 5px
  z-index 100

.uc-select-simple__option
  white-space nowrap
  padding 10px 12px
  &:hover
    background-color $col_light_selection

</style>
