const SESSION = 'sessionStorage'
const LOCAL = 'localStorage'
const ERR_MESS = 'Local storage is not available'

const getIt = (storage, key, json = false) => {
  try {
    return json ? JSON.parse(window[storage].getItem(key)) : window[storage].getItem(key)
  } catch (error) {
    console.error(ERR_MESS, error) // eslint-disable-line no-console
    return false
  }
}

const setIt = (storage, key, value) => {
  try {
    window[storage].setItem(key, value)
    return true
  } catch (error) {
    console.error(ERR_MESS, error) // eslint-disable-line no-console
    return false
  }
}

const removeIt = (storage, key) => {
  try {
    window[storage].removeItem(key)
    return true
  } catch (error) {
    console.error(ERR_MESS, error) // eslint-disable-line no-console
    return false
  }
}

export const sessionStore = {
  set: (key, value) => setIt(SESSION, key, value),
  get: key => getIt(SESSION, key),
  getJSON: key => getIt(SESSION, key, true),
  remove: key => removeIt(SESSION, key)
}

export const localStore = {
  set: (key, value) => setIt(LOCAL, key, value),
  get: key => getIt(LOCAL, key),
  getJSON: key => getIt(LOCAL, key, true),
  remove: key => removeIt(LOCAL, key)
}
