<template>
  <div>
    <section ref="builder" class="cat-builder" @click="setFocus" @keydown="keydownHandler">
      <div ref="inner" class="cat-builder__inner" :style="{transform: xPosition}">
        <div class="cat-builder__header"></div>
        <tree-section v-for="(section, index) in treeSections" ref="section" :key="index" :section="section" @nodeSelected="nodeSelected" @needsMoreCats="loadMoreCats"/>
      </div>
      <transition name="fade">
        <div v-show="showScroll" class="builder__scrollbar">
          <div class="builder__scroll-handle" :class="{scrolling: scrolling}" ref="scroller" :style="{width: `${scrollerWidthPercent}%`, transform: `translate3d(${scrollerPosition}px, 0, 0)`}"></div>
        </div>
      </transition>
    </section>
  </div>
</template>

<script>
import TreeSection from './tree-section.vue'
import MapCats from '../../js/map-category-response'
import MapMedia from '../../js/map-media-response'
import Constants from '../../../shared/js/constants'
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'content-navigator',
  data () {
    return {
      builderWidth: 0,
      visibleSections: 0,
      slidePosition: 0,
      sectionClicked: 0,
      scrolling: false,
      scrollInit: {
        mouseX: 0,
        scrollerPos: 0,
        slidePos: 0
      },
      scrollMove: 0,
      isLoadingMoreItems: false
    }
  },
  props: {
    dataUrl: String
  },
  computed: {
    ...mapGetters('navigator', ['treeSections', 'displayedNodePosition', 'sectionWidth', 'displayedNode']),
    ...mapGetters(['contentLanguage']),
    xPosition () {
      return `translate3d(-${this.slidePosition * this.sectionWidth}px, 0, 0)`
    },
    sectionsInTree () {
      return this.treeSections.length
    },
    showScroll () {
      return (this.sectionsInTree * this.sectionWidth) > (this.builderWidth + 50)
    },
    scrollerWidthPercent () {
      return (this.builderWidth / (this.sectionsInTree * this.sectionWidth)) * 100
    },
    scrollerWhiteSpace () {
      return this.builderWidth - (this.scrollerWidthPercent * this.builderWidth) / 100 // remaining horizontal space
    },
    scrollerPageSize () {
      return this.scrollerWhiteSpace / (this.sectionsInTree - this.visibleSections)
    },
    scrollerPosition () {
      if (!this.scrolling) {
        const xPos = this.scrollerPageSize * this.slidePosition
        return xPos
      }
      return this.scrollMove
    }
  },
  methods: {
    ...mapMutations('navigator', ['setSectionWidth', 'setSectionsInTree', 'addItemsToSectionInTree',
      'selectNode', 'resetDisplayedNode']),
    async loadMoreCats (cat) {
      if (cat.endOfList) return
      if (this.isLoadingMoreItems) return
      this.isLoadingMoreItems = true
      try {
        await this.getMoreRootItems(cat)
      } finally {
        this.isLoadingMoreItems = false
      }
      const section = this.$refs.section[cat.position]
      if (section) {
        section.checkIfMoreItemsNeeded.bind(section)()
      }
    },
    async getSectionData (endpoint, item, marker, sectionIndex) { // marker stays for pagination later
      const result = await this.axios.get(endpoint)
      const mappedResult = MapMedia(MapCats(result.data))
      return {
        position: sectionIndex,
        cat: item,
        elements: mappedResult.filter(x => x.pickerNodeType === Constants.ImagePicker.Folder),
        allElements: mappedResult.filter(x => x.pickerNodeType !== Constants.ImagePicker.File),
        categoriesCount: +result.headers['x-total-count']
      }
    },

    initRootItems () {
      const cat = {
        name: this.$t('contentNavigator.media'),
        nodeType: 'root'
      }
      const item = {
        name: this.$t('contentNavigator.home'),
        hasChildren: true,
        nodeType: 'Folder',
        pickerNodeType: 'Folder',
        id: '-1'
      }
      const elements = MapMedia(MapCats([item]))

      const sectionData = {
        position: 0,
        cat: cat,
        elements: elements,
        allElements: elements,
        categoriesCount: 1
      }

      this.setSectionsInTree(sectionData)
      if (sectionData.elements.length) {
        this.selectNodeWithPosition(0, 0)
      } else {
        this.resetDisplayedNode()
      }
    },

    async getRootItems (cat) {
      const endpoint = `${this.dataUrl}`
      const sectionData = await this.getSectionData(endpoint, cat, null, 1)
      this.setSectionsInTree(sectionData)
      this.resetScroll(2)
    },

    // empty for now, inspiration for pagination later
    async getMoreRootItems (cat, marker) {
      // const endpoint = `${this.dataUrl}/Image/${cat.guid}`
      // const sectionData = await this.getSectionData(endpoint, cat, marker, 1)
      // this.addItemsToSectionInTree(sectionData)
    },

    async getItems (cat, sectionIndex) {
      const endpoint = `${this.dataUrl}/${cat.nodeType}/${cat.guid}`
      const sectionData = await this.getSectionData(endpoint, cat, null, sectionIndex + 1)
      this.setSectionsInTree(sectionData)
      this.setWidth()
      this.slideSections()
      this.resetScroll(sectionIndex + 1)
    },

    // empty for now, inspiration for pagination later
    async getMoreItems (cat, marker) {
      // const endpoint = `${this.childrenUrl}/Image/${cat.guid}`
      // const sectionData = await this.getSectionData(endpoint, cat, marker, cat.position)
      // this.addItemsToSectionInTree(sectionData)
      // this.setWidth()
      // this.slideSections()
    },

    setWidth () {
      this.builderWidth = this.$refs.builder.offsetWidth
      this.visibleSections = Math.round(this.builderWidth / 300) // 300 being a sensible preferred width
      this.setSectionWidth(Math.ceil(this.builderWidth / this.visibleSections))
    },

    nodeSelected ({ cat, parentGuid }) {
      const sectionIndex = this.treeSections.findIndex(x => x.guid === parentGuid)
      this.selectNode({
        parentGuid,
        catGuid: cat.guid
      })
      if (sectionIndex === 0) {
        this.getRootItems(cat)
      } else {
        this.getItems(cat, sectionIndex)
      }
      this.sectionClicked = sectionIndex
    },

    getSectionScrollPosition (sectionIndex) {
      if (this.$refs.section[sectionIndex]) {
        return this.$refs.section[sectionIndex].getScrollPosition()
      } else {
        return 0
      }
    },

    setSectionScrollPosition (sectionIndex, scrollTop) {
      if (this.$refs.section[sectionIndex]) {
        return this.$refs.section[sectionIndex].setScrollPosition(scrollTop)
      }
    },

    resetScroll (sectionIndex) {
      const sectionElements = this.$refs.section
      if (sectionElements && sectionElements.length > sectionIndex) {
        sectionElements[sectionIndex].resetScroll()
      }
    },

    setFocus (event) {
      this.$nextTick(() => {
        if (event && event.target.classList.contains('cat-builder__node')) return // node clicked, handled by own event listener
        const nodeElem = document.getElementById(this.displayedNode.guid)
        if (!nodeElem) return
        nodeElem.focus()
      })
    },

    slideSections () {
      const triggerPos = (this.slidePosition + this.visibleSections) - 1
      if (this.sectionClicked === triggerPos && this.sectionsInTree > (triggerPos + 1)) {
        this.slidePosition += 1
        return
      }
      this.slidePosition = Math.max(this.sectionsInTree - this.visibleSections, 0)
    },

    selectNodeWithPosition (section, index) {
      const safeSection = Math.min(Math.max(section, 0), this.sectionsInTree - 1)
      const treeSection = this.treeSections[safeSection]
      const safeIndex = Math.min(Math.max(index, 0), treeSection.categories.length - 1)
      const hasChangedEffectively = this.displayedNodePosition.section !== safeSection || this.displayedNodePosition.index !== safeIndex
      const isRootNode = this.displayedNodePosition.section === 0 && this.displayedNodePosition.index === 0
      if (hasChangedEffectively || isRootNode) {
        this.nodeSelected({
          cat: treeSection.categories[safeIndex],
          parentGuid: treeSection.guid
        })
        this.setFocus()
      }
    },

    setupScrollHandler () {
      this.$refs.scroller.addEventListener('mousedown', event => {
        this.scrollInit.scrollerPos = this.scrollerPosition
        this.scrollInit.mouseX = event.clientX
        this.scrollInit.slidePos = this.slidePosition
        this.scrollMove = this.scrollerPosition
        this.scrolling = true
        window.addEventListener('mousemove', this.scrollMoveHandler)
      })
      window.addEventListener('mouseup', event => {
        this.scrolling = false
        window.removeEventListener('mousemove', this.scrollMoveHandler)
        window.onmousemove = null
      })
    },

    scrollMoveHandler (event) {
      event.preventDefault()
      const deltaX = event.clientX - this.scrollInit.mouseX
      this.scrollMove = Math.min(Math.max(deltaX + this.scrollInit.scrollerPos, 0), this.scrollerWhiteSpace)
      const pages = Math.round((this.scrollMove - this.scrollInit.scrollerPos) / this.scrollerPageSize)
      this.slidePosition = this.scrollInit.slidePos + pages
    },

    keydownHandler (e) {
      let sectionIndex = this.displayedNodePosition.section
      let index = this.displayedNodePosition.index
      switch (e.key) {
        case 'ArrowLeft':
          if (sectionIndex === 0) break
          sectionIndex -= 1
          index = this.treeSections[sectionIndex].categories.findIndex(x => x.selected)
          break
        case 'ArrowUp':
          index -= 1
          break
        case 'ArrowRight':
          if (this.treeSections[sectionIndex + 1].categories.length) {
            sectionIndex += 1
          }
          break
        case 'ArrowDown':
          index += 1
          break
        default:
          return
      }
      e.preventDefault()
      this.selectNodeWithPosition(sectionIndex, index)
    }
  },
  components: {
    TreeSection
  },
  mounted () {
    this.initRootItems()
    this.setWidth()
    window.addEventListener('resize', () => {
      this.setWidth()
      this.slideSections()
    })
    this.setupScrollHandler()
  }
}
</script>

<style lang="stylus" scoped>
.cat-builder
  box-shadow 0 4px 6px #eee
  position relative
  background-color #fff
  overflow hidden
  min-height 200px
.cat-builder__inner
  white-space nowrap
  transition transform 300ms $easeInOutCirc
  transform translate3d(0, 0, 0)
.cat-builder__header
  position absolute
  width 100%
  height $header_height
  top 0
  background-color lighten($col_table_header_bg, 30%)
  z-index -1
.builder__scrollbar
  height 13px
  border-top solid 1px $col_light_gray
.builder__scroll-handle
  width 80%
  border-radius 15px
  background-color $col_light_gray
  height 12px
  border solid 1px #fff
  transition all 300ms ease
  cursor pointer
  &.scrolling
    transition none

</style>
