<template>
  <button class="uc-button" :type="type" :class="{ primary }" @click="clicked">{{text}}</button>
</template>

<script>
export default {
  name: 'uc-button',
  props: {
    text: String,
    disabled: Boolean,
    primary: Boolean,
    type: String
  },
  methods: {
    clicked (e) {
      this.$emit('click', e)
    }
  }
}
</script>

<style lang="stylus" scoped>
  .uc-button
    primary-details-text()
    border none
    outline none
    background-color #fff
    padding 6px 32px
    border-radius 9999px // always rounded borders, regardless the size
    cursor pointer
    transition background-color 150ms ease
    &:hover
      text-decoration underline
    &.primary
      background-color $ucommerce-blue
      color #fff
      &:hover
        background-color lighten($ucommerce-blue, 8)
        text-decoration none
    &:active
      box-shadow none
      transform scale(0.95)
</style>
