<template>
  <label class="input-label">{{ computedLabel }}</label>
</template>

<script>
export default {
  name: 'uc-label',
  props: {
    name: String,
    definition: {
      type: Object,
      default () {
        return {}
      }
    },
    language: String
  },
  computed: {
    computedLabel () {
      const lang = this.definition.multilingual ? `(${this.language})` : ''
      return `${this.definition.label} ${lang}`
    }
  }
}
</script>

<style lang="stylus" scoped>
  .input-label
    font-weight 600
    font-size 0.625rem
    color $col_medium_gray
    display block
    margin-bottom: 4px
</style>
